.row-main {
	@include clearfix();
	position: relative;
	margin: 0;
	padding: 0;
	margin: 0 auto;
	max-width: 1240px;
	padding: 0 15px;
	&--narrow {
		max-width: 770px;
	}
	&--full{
		max-width: none;
	}
	&--wide{
		max-width: 1920px;
	}

	@media (min-width: $tablet){
		padding: 0 25px;
	}

	@media (min-width: $desktop){
		padding: 0 35px;
	}

	.row-main {
		padding: 0;
	} /* zjisti proč? */
}

.row{
	@include clearfix();
	position: relative;
	margin: 0;
	padding: 0;
}

.grid{
	@extend %reset-ol;
	@extend %grid;
	margin-left: -20px;
	&__cell{
		@extend %reset-ol-li;
		@extend %grid__cell;
		width: 100%;
		position: relative;
		border-left: 20px solid transparent;
		&.size--auto{
			width: auto;
		}
	}
	&--lg{
		@media (min-width: $desktop){
			margin-left: -40px;
		}
	}
	&--lg > &__cell{
		@media (min-width: $desktop){
			border-left-width: 40px;
		}
	}
	&--xl{
		margin-left: -30px;
		@media (min-width: $desktop){
			margin-left: -80px;
		}
	}
	&--xl > &__cell{
		border-left-width: 30px;
		@media (min-width: $desktop){
			border-left-width: 80px;
		}
	}
	&--scroll{
		@extend %grid--scroll;
	}
	&--center{
		text-align: center;
	}
	&--right{
		text-align: right;
	}
	&--middle > &__cell{
		vertical-align: middle;
	}
	&--bottom > &__cell{
		vertical-align: bottom;
	}
	&--nogutter{
		margin-left: 0;
	}
	&--nogutter > &__cell{
		border-left-width: 0;
	}
}

// selector, separator, columns, rule, coef, otherRules
@include grid('.size--xs--', '-', 2, 'width');
@include grid('.push--xs--', '-', 2, 'left');
@include grid('.pull--xs--', '-', 2, 'left', -1);
@media (min-width: $mobileL){
	@include grid('.size--s--', '-', 2, 'width');
	@include grid('.push--s--', '-', 2, 'left');
	@include grid('.pull--s--', '-', 2, 'left', -1);
}
@media (min-width: $tablet){
	@include grid('.size--m--', '-', 4, 'width');
	@include grid('.push--m--', '-', 4, 'left');
	@include grid('.pull--m--', '-', 4, 'left', -1);
	@include grid('.size--m--', '-', 12, 'width');
	@include grid('.push--m--', '-', 12, 'left');
	@include grid('.pull--m--', '-', 12, 'left', -1);
}
@media (min-width: $desktop){
	@include grid('.size--l--', '-', 12, 'width');
	@include grid('.push--l--', '-', 12, 'left');
	@include grid('.pull--l--', '-', 12, 'left', -1);
	@include grid('.push-m--l--', '-', 12, 'margin-right', -1);
	@include grid('.pull-m--l--', '-', 12, 'margin-left', -1);

	// makeshift 5-column grid support
	.size--l--2\.4-12 {
        width: 20%;
    }
}
