.box-intro{
	position: relative;
	color: #fff;
	text-align: center;
	margin-bottom: $mMarginSmall;
	&__img{
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		background-size: cover;
		&:before{
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background: linear-gradient(to bottom, rgba(#000, 0), rgba(#000, 1));
			opacity: .4;
		}
	}
	&__title{
		font-size: 32px;
		font-family: $fontMuni;
		color: #fff;
	}
	br{
		display: none;
	}
	&__text{
		font-size: 18px;
		font-weight: bold;
		max-width: 830px;
		margin-left: auto;
		margin-right: auto;
	}
	&__wrap{
		padding: 50px 0;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	&__next{
		font-size: 30px;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 10px;
		margin-bottom: 0;
		a{
			color: $muni;
		}
	}

	@media (min-width: $tablet){
		margin: 0 0 $mMargin;
		&__wrap{
			padding: 120px 0;
			min-height: 900px;
		}
		&__title{
			font-size: 54px;
		}
		&__text{
			font-size: 24px;
		}
		&__next{
			font-size: 58px;
			bottom: 60px;
		}
	}

	@media (min-width: $desktopWide){
		&__title{
			font-size: 70px;
		}
		br{
			display: inline;
		}
	}
}
