$s: '.box-article-grid';

.box-article-grid{
	margin: 0 0 $mMarginSmall;
	display: block;
	position: relative;
	font-size: 14px;
	&__inner {
		color: $color;
		text-decoration: none;
		@include hover() {
			&[href]:hover {
				color: $color;
				#{$s}__title{
					color: $linkHover;
					text-decoration: underline;
				}
			}
		}
		&[href]{
			#{$s}__title {
				color: $muni;
			}
			&:visited{
				#{$s}__title {
					color: $linkVisited;
				}
			}
			@include hover() {
				&:visited:hover {
					#{$s}__title {
						color: $linkHover;
					}
				}
			}
		}
	}
	&__content{
		padding: 10px 0 15px;
		> :last-child{
			margin-bottom: 0;
		}
	}
	&__title{
		font-size: 18px;
		margin: .5em 0;
		transition: color .2s;
		position: relative;
		padding-bottom: 15px;
		-moz-transition: none;
		&:before{
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 40px;
			height: 2px;
			background: $color;
		}
		&--big{
			font-size: 24px;
		}
		&--bigger{
			font-size: 21px;
		}
	}
	&__img{
		margin: 0;
		text-align: center;
		overflow: hidden;
		&--primary {
		    background: $primary;
	    }
	    &--muni {
		    background: $muni;
	    }
	    &--law {
		    background: $law;
	    }
	    &--med {
		    background: $med;
	    }
	    &--sci {
		    background: $sci;
	    }
	    &--fi {
		    background: $fi;
		    color: #000;
	    }
	    &--fss {
		    background: $fss;
	    }
	    &--fsps {
		    background: $fsps;
	    }
	    &--phil {
		    background: $phil;
	    }
	    &--ped {
			background: $ped;
			color: #000;
	    }
	    &--econ {
		    background: $econ;
		}
		&--pharm {
		    background: $pharm;
	    }
	    &--dark {
		    background: $dark;
	    }
	    &--color {
		    background: $color;
	    }
	    &--grey-dark {
		    background: $greyDark;
	    }
	    &--grey-light {
		    background: $greyLight;
	    }
        &--white {
            background: #fff;
            border-left: 1px solid $borderPrimary;
            border-top: 1px solid $borderPrimary;
            border-right: 1px solid $borderPrimary;
        }
	}

	&--noborder{
		#{$s}__img,
		#{$s}__content{
			border: none;
		}
	}

	@media(max-width: $desktopWide - 1){
		&__img{
			img{
				min-width: 100%;
			}
		}
	}

	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		&__content{
			padding: 15px 0 20px;
		}

		&--big{
			font-size: 16px;
			#{$s}__title{
				font-size: 24px;
				&--big{
					font-size: 32px;
				}
			}
		}
	}

	@media(min-width: $desktop){
		&__content{
			padding: 20px 0;
		}
		&--big{
			#{$s}__content{
				padding: 25px 0 30px 0;
			}
		}
	}

	&--noimg{
		#{$s}__content{
			padding-top: 0;
		}
	}
}
