$s: '.paging';

.paging{
	margin: 0 0 $mMargin;
	color: $dark;
	&__pages{
		font-size: 14px;
		font-weight: bold;
		display: flex;
		align-items: center;
		&__prev,
		&__next{
			text-decoration: none;
			-moz-transition: none;
			.icon{
				transition: color .2s;
			}
			&:visited{
				color: $link;
			}
			@include hover() {
				&:hover,
				&:visited:hover {
					.icon{
						color: $linkHover;
					}
				}
			}
		}
		&__prev{
			margin-right: 10px;
		}
		&__next{
			margin-left: 10px;
		}
		&__item{
			font-family: $fontMuni;
			text-align: center;
			min-width: 40px;
			padding: 0 5px;
			text-decoration: none;
			line-height: 40px;
			border: 1px solid $border;
			margin: -1px 0 0 -1px;
			transition: color .2s, background .2s;
			&[href]{
				@include hover() {
					&:hover {
						background: $muni;
						color: #fff;
					}
				}
			}
			&--dots{
				color: $muni;
			}
			&.is-active{
				line-height: 40px;
				color: $dark;
				background: #fff;
				border: 1px solid $border;
			}
		}
	}
	&__pager{
		font-size: 16px;
		font-family: $fontMuni;
		font-weight: bold;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		&__item{
			margin-left: 20px;
		}
	}

	&--center{
		flex-direction: column;
		#{$s}__pages{
			justify-content: center;
		}
		#{$s}__pager{
			justify-content: center;
			margin-top: 15px;
			&__item{
				margin-left: 7px;
				margin-right: 7px;
			}
		}
	}

	@media(max-width: $tablet - 1){
		text-align: center;
		#{$s}__pages,
		#{$s}__pager{
			justify-content: center;
			flex-wrap: wrap;
		}
		#{$s}__pager{
			margin-top: 15px;
			&__item{
				margin-left: 7px;
				margin-right: 7px;
			}
		}
	}

	@media(max-width: $mobileL - 1){
		#{$s}__pages{
			padding-top: 50px;
			position: relative;
			&__prev,
			&__next{
				position: absolute;
				top: 0;
			}
			&__prev{
				left: 0;
			}
			&__next{
				right: 0;
			}
		}
		#{$s}__pager{
			margin-top: 10px;
		}
	}

	@media (min-width: $tablet) {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
}
