$s: '.box-testimonial';

.box-testimonial{
	margin: 0 0 $mMarginSmall;
	padding: 10px 0;
	border: 2px solid $color;
	border-width: 2px 0;
	&:after{
		content: '';
		display: table;
		clear: both;
	}
	> :last-child{
		margin-bottom: 0;
	}
	&__comment{
		font-weight: bold;
		font-size: 18px;
		margin-bottom: 15px;
	}
	&__author{
		font-size: 14px;
		font-style: normal;
		display: block;
		font-family: $font;
		margin: 0 0 1.25em;
		a{
			text-decoration: none;
			color: $color;
			display: inline-block;
			vertical-align: top;
		}
		strong{
			font-size: 16px;
			color: $dark;
		}
		&.with-img{
			position: relative;
			min-height: 46px;
			padding: 8px 0 8px 80px;
			a{
				margin-left: -80px;
				padding-left: 80px;
			}
			#{$s}__author__img{
				position: absolute;
				left: 0;
				top: 0;
				width: 62px;
				text-align: center;
				img{
					border-radius: 100px;
				}
			}
		}
	}

	&--avatar,
	&--bordered,
	&--background{
		border: none;
		#{$s}__comment{
			margin-bottom: 18px;
			border: 2px solid $color;
			border-width: 2px 0;
			padding: 10px 0;
		}
	}

	&--avatar{
		border: none;
		#{$s}__img{
			text-align: center;
			img{
				border-radius: 50%;
			}
		}
	}

	&--img{
		position: relative;
		#{$s}__img{
			margin: 0 0 15px;
		}
	}

	&--bordered{
		border: 1px solid $background;
		background: #fff;
		padding: 20px;
		&#{$s}--img{
			border-width: 1px 0;
		}
		&#{$s}--avatar{
			padding: 20px;
		}
	}

	&--bordered-top-bottom{
		border: 2px solid $border;
		border-width: 2px 0;
		padding: 20px;
	}

	&--background{
		border: 0;
		background: $muni;
		color: #fff;
		padding: 20px;
		#{$s}__author a,
		#{$s}__author strong,
		#{$s}__comment{
			color: #fff;
			border-color: #fff;
		}
		&#{$s}--avatar{
			padding: 20px;
		}
	}

	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		padding: 20px 0;
		&__author{
			font-size: 16px;
		}
		&--avatar,
		&--bordered,
		&--background{
			padding: 20px 0;
		}
		&--avatar{
			padding-left: 210px;
			#{$s}__img{
				float: left;
				margin-left: -210px;
				width: 180px;
			}
		}

		&--bordered,
		&--background{
			padding: 30px;
			&#{$s}--avatar{
				padding: 30px 30px 30px 240px;
				#{$s}__img{
					margin-left: -210px;
				}
			}
		}

		&--bordered-top-bottom{
			padding: 30px;
		}

		&__comment{
			font-size: 21px;
		}
		&--small &__comment {
			font-size: 18px;
		}
	}

	@media(min-width: $desktop){
		&--avatar{
			padding-left: 230px;
			#{$s}__img{
				margin-left: -230px;
				width: 180px;
			}
		}

		&--img{
			.row-main{
				&:before{
					content: '';
					height: 490px;
					display: inline-block;
					vertical-align: middle;
					margin-left: -.2em;
				}
			}
			#{$s}__content{
				padding: 20px 15px 0 0;
				width: 50%;
				display: inline-block;
				vertical-align: middle;
			}
			#{$s}__img{
				margin: 0 0 0 15px;
				position: absolute;
				left: 50%;
				top: 0;
				bottom: 0;
				right: 0;
				overflow: hidden;
				img{
					position: absolute;
					left: 50%;
					top: 50%;
					transform: translate(-50%, -50%);
					max-width: none;
					min-height: 100%;
					width: auto;
				}
			}
		}

		&--bordered,
		&--background{
			&#{$s}--avatar{
				padding: 30px 50px 40px 280px;
				#{$s}__img{
					margin-left: -230px;
					width: 180px;
				}
			}
		}

		&__comment{
			font-size: 24px;
		}
		&--small &__comment {
			font-size: 18px;
		}
	}
}
