$s: '.box-grid';

.box-grid{
	margin: 0 0 $mMarginSmall;
	.grid {
		overflow: hidden;
	}
	&__inner{
		font-size: 14px;
		display: block;
		text-decoration: none;
		color: $color;
		text-align: center;
		border: 1px solid $border;
		border-width: 1px 0 0 1px;
		padding: 15px 15px;
		margin: -2px 0 0 -2px;
		&:first-child {
			margin-top: -2px;
		}
		&--left {
			text-align: left;
		}
		> *:last-child {
			margin-bottom: 0;
		}
		&[href] {
			-moz-transition: none;
			h1,h2,h3,h4,h5,h6 {
				text-decoration: none;
				color: $muni;
				transition: color .2s;
				-moz-transition: none;
			}
			@include hover() {
				&:hover {
					color: inherit;
					h1,h2,h3,h4,h5,h6{
						color: inherit;
						text-decoration: underline;
					}
				}
			}
			&:visited{
				h1,h2,h3,h4,h5,h6{
					color: $linkVisited;
				}
			}
			@include hover() {
				&:visited:hover {
					h1,h2,h3,h4,h5,h6{
						color: $linkHover;
					}
				}
			}
		}
		.white, .box-bg--color-light {
			&[href] {
				h1,h2,h3,h4,h5,h6 {
				    color: #fff;
				}
			}
		}
	}

	&--bordered{
		.grid{
			background: #fff;
		}
	}

	&--background{
		.grid{
			background: $muni;
		}
		#{$s}__inner{
			color: $whiteInvertable;
			border-color: rgba(#fff,.2);
			&[href] {
				@include hover() {
					&:hover {
						color: #fff;
						h1,h2,h3,h4,h5,h6{
							color: #fff;
							text-decoration: none;
						}
					}
				}
			}
		}
	}

	@media(min-width: $mobileL){
		&--bordered{
			.grid{
				outline: 1px solid $border;
				border: 15px solid #fff;
			}
		}
		&--background{
			.grid{
				border: 15px solid $muni;
			}
		}
	}

	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		&--bordered,
		&--background{
			.grid{
				border-left-width: 0;
				border-right-width: 0;
			}
		}
	}

	@media(min-width: $desktop){
		&:not(#{$s}--bordered):not(#{$s}--background) {
			.grid__cell:first-child {
				#{$s}__inner--left {
					padding-left: 2px;
				}
			}
		}
		&--bordered,
		&--background{
			.grid{
				border-top-width: 20px;
				border-bottom-width: 20px;
			}
		}
	}

	@media(min-width: $desktopWide){
		&--bordered,
		&--background{
			.grid{
				border-top-width: 50px;
				border-bottom-width: 50px;
			}
		}
		&__inner{
			padding: 15px 40px;
		}

	}
}
