.table-horizontal{
	position: relative;
	overflow: hidden;
	overflow-x: auto;
	// &:before{
	// 	content: "";
	// 	position: absolute;
	// 	top: 0;
	// 	right: 0;
	// 	bottom: 0;
	// 	width: 40px;
	// 	background: linear-gradient(to right, rgba(0,0,0,0) 0%, $greyLight 100%);
	// 	z-index: 5;
	// }
	table{
		display: table !important;
		width: 100% !important;
	}
	thead{
		display: table-header-group !important;
		width: auto !important;
	}
	tbody{
		display: table-row-group !important;
		width: auto !important;
	}
	tfoot{
		display: table-footer-group !important;
		width: auto !important;
	}
	tr{
		display: table-row !important;
		width: auto !important;
	}
	th,
	td{
		display: table-cell;
		padding: 10px 25px;
		width: auto !important;
		&:first-child{
			padding-left: 0;
		}
		&:last-child{
			padding-right: 0;
		}
	}
	@media(max-width: $mobileL - 1){
		tbody,
		tfoot{
			td{
				&:last-child{
					border-bottom: none;
				}
			}
		}
	}
}
