$s: '.box-category';

.box-category{
	margin: 0 0 $mMarginSmall;
	border: 1px solid transparent;
	text-align: center;
	display: flex;
	justify-content: center;
	background: $muni;
	position: relative;
	&__inner {
		text-decoration: none;
		color: #fff;
		padding: 30px 15px;
		flex: 1;
		display: flex;
		flex-direction: column;
		justify-content: center;
		@include hover() {
			&[href]:hover {
				text-decoration: none;
				color: #fff;
				#{$s}__title{
					text-decoration: underline;
				}
			}
		}
	}
	&:before{
		content: '';
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		background: url('#{$img}bg/box-category.jpg') 50% 50% no-repeat;
		background-size: cover;
		opacity: .1;
	}
	> *{
		position: relative;
		z-index: 2;
	}
	&--no-img{
		&:before{
			display: none;
		}
	}
	p{
		margin-bottom: 30px;
		&:last-child{
			margin-bottom: 0;
		}
	}
	&__title{
		color: #fff;
		font-size: 24px;
		margin: 0 0 30px;
	}

	&--white{
		background-color: #fff;
		border-color: $border;
		#{$s}__inner {
			color: $color;
			@include hover() {
				&[href]:hover{
					color: $color;
				}
			}
		}
		#{$s}__title{
			color: $muni;
		}
	}

	&--primary{
		background-color: $muni;
		border-color: $muni;
		color: $color;
	}

	&--img{
		display: flex;
		justify-content: center;
		//align-items: center;
		flex-direction: column;
		border: none;
		background-size: cover;
		background-position: center center;
		&:before{
			display: none;
		}
		*:last-child{
			margin-bottom: 0;
		}
	}
	&--bg{
		&:after{
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background: #000;
			opacity: .4;
		}
	}
	&--end{
		#{$s}__inner{
			justify-content: flex-start;
		}
		p:last-child{
			display: flex;
			justify-content: center;
			align-items: flex-end;
			flex: 9999;
		}
	}

	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		&__title{
			&--big{
				font-size: 30px;
				line-height: 36px;
			}
		}

		&__inner{
			padding: 55px 20px;
		}
	}

	@media(min-width: $desktop){
		&__inner{
			padding: 55px 20px;
		}
	}
}
