$s: '.box-tabs';

.box-tabs{
	margin: 0 0 $mMarginSmall;
	&__menu{
		@extend %reset-ul;
		font-family: $fontSecondary;
		border-bottom: 2px solid $greyDark;
		display: flex;
		justify-content: flex-start;
		align-items: flex-end;
		&__item{
			@extend %reset-ul-li;
		}
		&__link{
			display: block;
			text-decoration: none;
			padding: 17px 0 15px;
			position: relative;
			border: 2px solid transparent;
			border-width: 2px 2px 0;
			margin-bottom: -2px;
			transition: color .2s, border-color .2s, background .2s;
			&:before{
				content: '';
				position: absolute;
				top: -2px;
				left: -2px;
				right: -2px;
				height: 2px;
				background: transparent;
				transition: background .2s;
			}
			&.is-active{
				background: $greyLight;
				color: $color;
				font-weight: bold;
				border-color: $greyDark;
				&:before{
					background: $color;
				}
				@include hover() {
					&:hover {
						color: $color;
					}
				}
			}
			&.is-disabled{
				cursor: default;
				color: #7d7d7d;
				text-decoration: none;
			}
		}
	}
	&__content {
		> :last-child {
			margin-bottom: 0;
		}
	}
	@media (max-width: $tablet - 1){
		border-bottom: 2px solid $greyDark;
		border-left: 2px solid $greyDark;
		border-right: 2px solid $greyDark;
		&__menu,
		.menu-filter{
			display: none;
		}
		&__responsive-link{
			text-decoration: none;
			display: block;
			position: relative;
			padding: 14px 70px 13px 20px;
			transition: color .2s, background .2s;
			@include hover() {
				&:hover {
					background: $elementsHover;
				}
			}
			.icon{
				position: absolute;
				right: 0;
				top: 0;
				bottom: -2px;
				width: 50px;
				font-size: 0px;
				color: $muni;
				border-left: 2px solid $border;
				border-bottom: 2px solid $border;
				&:before{
					position: absolute;
					left: 50%;
					top: 50%;
					margin: -13px 0 0 -7px;
    				font-size: 28px;
				}
			}
			&.is-disabled{
				text-decoration: none;
				opacity: .5;
				.icon{
					display: none;
				}
			}
		}
		&__fragment{
			position: relative;
			background: #fff;
			border-top: 2px solid $greyDark;
			&:before {
				content: '';
				position: absolute;
				top: -2px;
				left: -2px;
				right: -2px;
				height: 2px;
				background: transparent;
				transition: background .2s;
				z-index: 2;
			}
			&.is-active{
				&:before {
					background: $color;
				}
				> #{$s}{
					&__content{
						position: relative;
						left: auto;
						top: auto;
						width: auto;
						height: auto;
						overflow: visible;
					}
					&__responsive-link{
						color: $color;
						font-weight: bold;
						.icon{
							&:before{
								color: $color;
								transform: rotate(180deg);
							}
						}
					}
				}
			}
		}
		&__content{
			padding: 12px 20px;
			.js &{
				position: absolute;
				width: 0;
				height: 0;
				overflow: hidden;
				left: -5000px;
				top: -5000px;
			}
		}
	}

	@media (min-width: $tablet){
		margin: 0 0 $mMargin;
		&__menu{
			&__link {
				padding: 12px 20px;
			}
		}
		&__responsive-link{
			display: none;
		}
		&__fragment{
			padding: 25px 0 5px;
			&--bordered{
				padding: 25px 35px 5px;
				border: 2px solid $greyDark;
				border-width: 0 2px 2px;
			}
			&--bordered-bottom{
				border-bottom: 2px solid $greyDark;
			}
			.js &{
				position: absolute;
				width: 0;
				height: 0;
				overflow: hidden;
				left: -5000px;
				top: -5000px;
				&.is-active{
					position: relative;
					left: auto;
					top: auto;
					width: auto;
					height: auto;
					overflow: hidden;
				}
			}
		}
	}
}
