$s: '.share';

.share{
	font-size: 0px;
	margin: 0 0 $mMarginSmall;
	a:visited{
		color: #fff !important;
	}
	.icon-facebook{
		background: $facebook;
		@include hover() {
			&:hover {
				background: darken($facebook, 10%);
			}
		}
	}
	.icon-twitter{
		background: $twitter;
		@include hover() {
			&:hover {
				background: darken($twitter, 10%);
			}
		}
	}
	.icon-linkedin{
		background: $linkedin;
		@include hover() {
			&:hover {
				background: darken($linkedin, 10%);
			}
		}
	}
	.icon-envelope{
		background: $mail;
		@include hover() {
			&:hover {
				background: darken($mail, 10%);
			}
		}
	}
	.icon-google-plus{
		background: $google;
		@include hover() {
			&:hover {
				background: darken($google, 10%);
			}
		}
	}
	.icon-instagram{
		background: $instagram;
		@include hover() {
			&:hover {
				background: darken($instagram, 10%);
			}
		}
	}
    .icon-youtube{
		background: $youtube;
		@include hover() {
			&:hover {
				background: darken($youtube, 10%);
			}
		}
	}
    .icon-github{
		background: $github;
		@include hover() {
			&:hover {
				background: darken($github, 10%);
			}
		}
	}
    .icon-rss{
		background: $rss;
		@include hover() {
			&:hover {
				background: darken($rss, 10%);
			}
		}
	}
	&__item{
		display: inline-block;
		vertical-align: top;
		width: 44px;
		line-height: 44px;
		font-size: 20px;
		text-align: center;
		color: #fff;
		transition: background .2s, color .2s;
		@include hover() {
			&:hover {
				color: #fff;
			}
		}
	}
	&__btn{
		background: $muni;
		color: $whiteInvertable;
		@include hover() {
			&:hover {
				background: $dark;
				color: #fff;
			}
		}
		&--text{
			display: inline-block;
			vertical-align: top;
			line-height: 44px;
			font-size: 16px;
			font-weight: bold;
			text-transform: uppercase;
			color: $whiteInvertable;
			text-decoration: none;
			padding: 0 30px 0 50px;
			transition: background .2s;
			position: relative;
			.icon{
				position: absolute;
				left: 30px;
				top: 50%;
				font-size: 16px;
				margin-top: -8px;
			}
		}
	}
	&__box{
		display: none;
		vertical-align: top;
	}
	&.is-open{
		#{$s}__btn{
			background: $dark;
		}
		#{$s}__box{
			display: inline-block;
		}
	}

	// big
	&--big{
		#{$s}__item{
			width: 90px;
			line-height: 56px;
			font-size: 30px;
		}
	}
}
