.link-icon{
	display: inline-block;
	vertical-align: top;
	white-space: nowrap;
	position: relative;
	max-width: 100%;
	overflow: hidden;
	text-overflow: ellipsis;
	-moz-transition: none;
	.icon{
		color: $muni;
		position: absolute;
		top: 50%;
		margin-top: -(7/18)+em;
		font-size: (16/18)+em;
	}
	&--before{
		padding-left: (28/18)+em;
		.icon{
			left: 1px;
		}
	}
	&--after{
		padding-right: (28/18)+em;
		.icon{
			right: 1px;
		}
	}
	&--before-small{
		padding-left: (18/18)+em;
	}
	&--after-small{
		padding-right: (18/18)+em;
	}
	&:visited{
		color: $linkVisited;
	}
	@include hover() {
		&:visited:hover {
			color: $linkHover;
		}
	}
}

.link-file{
	text-decoration: none;
	-moz-transition: none;
	&__name{
		text-decoration: underline;
		display: inline-block;
		vertical-align: top;
	}
	.file-type{
		top: -1px;
		margin-left: 3px;
	}
	&:visited{
		color: $linkVisited;
	}
	@include hover() {
		&:visited:hover {
			color: $linkHover;
		}
	}
}
