.box-poll{
	margin: 0 0 $mMarginSmall;
	font-size: 16px;
	&__question{
		font-weight: bold;
		margin-bottom: 20px;
	}
	&__list{
		@extend %reset-ul;
		margin: 0 0 15px;
		background: #fff;
		border: 1px solid $border;
	}
	&__item{
		@extend %reset-ul-li;
		border-top: 1px solid $border;
		font-weight: bold;
		&:first-child{
			border-top: none;
		}
	}
	&__inner{
		display: block;
		padding: 15px 45px 17px 15px;
		position: relative;
		color: $link;
		text-decoration: none;
		transition: background .2s, color .2s;
		&.is-active{
			color: $color;
			background: $elementsHover;
			.box-poll__icon{
				opacity: 1;
			}
		}
		@include hover() {
			&[href]:hover{
				color: $color;
				background: $elementsHover;
				.box-poll__icon{
					opacity: 1;
				}
			}
		}
	}
	&__answer{
		margin: 0;
		padding: 0 0 0 25px;
		position: relative;
	}
	&__number{
		position: absolute;
		left: 0;
		top: 0;
	}
	&__icon{
		position: absolute;
		right: 20px;
		top: 50%;
		font-weight: bold;
		font-size: 20px;
		margin-top: -10px;
		color: $primary;
		opacity: 0;
		transition: opacity .2s;
	}
	&__percent{
		margin-right: 45px;
		position: relative;
		font-size: 14px;
		color: $dark;
		height: 10px;
		margin-top: 10px;
		&__inner{
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			background: $primary;
		}
		&__number{
			font-family: $fontMuni;
			position: absolute;
			left: 100%;
			top: -5px;
			width: 45px;
			text-align: right;
			line-height: 20px;
			white-space: nowrap;
		}
	}
	&__info{
		margin: 0;
		font-size: 14px;
	}

	@media (min-width: $tablet){
		margin: 0 0 $mMargin;
		&__inner{
			padding: 15px 55px 17px 20px;
		}
		&__icon{
			right: 30px;
		}
	}
}
