$s: '.box-alternative';

.box-alternative {
	position: relative;
	display: block;
	margin: 0 0 $mMarginSmall;
	&__inner {
		display: block;
		text-decoration: none;
		@include hover() {
			&:hover {
				#{$s}__title {
					text-decoration: underline;
				}
			}
		}
		&:before {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background-image: linear-gradient(to top, rgba(#000, 0.5), rgba(#000, 0));
		}
	}
	&:not(.light-gradient) &__inner:before {
		background-image: linear-gradient(to top, rgba(#000, 1), rgba(#000, 0));
	}
	&__img {
		margin-bottom: 0;
		img {
			width: 100%;
			height: auto;
		}
	}
	&__title {
		font-size: 18px;
		margin-bottom: 0;
		color: #fff;
	}
	&__content {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		padding: 24px 20px 12px;
	}
	&__author {
		font-size: 16px;
		margin: 10px 0 0;
		color: #fff;
		position: relative;
		padding-top: 15px;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 35px;
			height: 1px;
			background: currentColor;
		}
	}
	&__desc{
		color: #fff;
		position: relative;
		padding-bottom: 30px;
		&:before {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 35px;
			height: 2px;
			background: currentColor;
		}
	}

	&--bg{
		background: $muni;
		#{$s}__inner:before{
			display: none;
		}
		#{$s}__content{
			position: static;
		}
	}

	&--reverse{
		background: #fff;
		border: 1px solid $border;
		#{$s}__content{
			position: static;
		}
		#{$s}__title{
			color: $muni;
		}
		#{$s}__author,
		#{$s}__desc{
			color: $color;
		}
	}

	&--xl{
		#{$s}__content{
			padding: 25px 20px;
		}
		#{$s}__title{
			font-size: 22px;
			margin-bottom: 15px;
		}
	}

	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		&--xl{
			#{$s}__content{
				padding: 45px 40px;
			}
			#{$s}__title{
				font-size: 30px;
				margin-bottom: 20px;
			}
		}
	}
}
