body{
	.gsc-control-cse.gsc-control-cse.gsc-control-cse{
		margin: 0 0 $mMarginSmall;
		padding: 0;
		border: none;
		background: none;
		font-size: 13px;
		color: $color;
		font-family: $font;
		line-height: 19px;
		.gsc-table-result,
		.gsc-thumbnail-inside,
		.gsc-url-top{
			padding: 0;
		}
		.gsc-results{
			width: 100%;
			padding: 0;
		}
		.gsc-above-wrapper-area,
		.gcsc-branding,
		.gsc-resultsHeader,
		.gsc-thumbnail{
			display: none;
		}
		.gs-snippet{
			margin: 0;
		}
		.gsc-table-result{
			font-size: 13px;
			color: $color;
			font-family: $font;
			line-height: 19px;
		}
		.gsc-webResult{
			table{
				margin: 0;
			}
			td{
				padding: 0;
			}
			.gsc-result{
				border: 0px !important;
				padding: 0;
				margin: 0 0 30px;
			}
			.gs-visibleUrl{
				color: #595959;
			}
			.gs-title{
				font-weight: bold;
				font-size: 18px;
				color: $link;
				text-decoration: none;
				margin: 0 0 3px;
				* {
					color: $link;
					transition: color .2s;
					text-decoration: none;
				}
				@include hover() {
					&:hover {
						* {
							color: $linkHover !important;
						}
					}
				}
			}
		}
		.gsc-results {
			.gsc-cursor-box{
				border-top: 1px solid $border;
				padding-top: 30px;
				line-height: 40px;
				margin: 30px 0 $mMargin;
				overflow: hidden;
				line-height: 40px;
				color: $dark;
				font-size: 16px;
				font-weight: bold;
				text-align: center;
				font-family: $fontMuni;
				.gsc-cursor{
					display: flex;
					align-items: center;
					justify-content: center;
				}
				.gsc-cursor-page{
					border: 1px solid $border;
					text-align: center;
					min-width: 40px;
					padding: 0 5px;
					margin: 0 0 0 -1px;
					color: $link;
					outline: none;
					text-decoration: none;
					transition: color .2s, background .2s;
					@include hover() {
						&:hover {
							color: $linkHover;
						}
					}
					&.gsc-cursor-current-page{
						color: $dark;
						background: none;
					}
				}
			}
		}
		.gs-webResult .gs-snippet{
			color: $color;
		}
		.gsc-webResult.gsc-result,
		.gsc-results .gsc-imageResult{
			background-color: transparent !important;
		}
		@media (min-width: $tablet) {
			margin: 0 0 $mMargin;
		}
	}
}
