.text-icon {
	display: inline-block;
	padding: 2px 0 2px 39px;
	position: relative;
	text-decoration: none;
	font-size: 14px;
	small {
		font-size: 12px;
		color: #aaa;
		font-weight: bold;
	}
	&__icon {
		position: absolute;
		top: 0;
		left: 0;
		width: 24px;
		height: 24px;
		line-height: 25px;
		font-weight: bold;
		font-family: $fontMuni;
		text-align: center;
		color: #CBD4E4;
		.icon {
			font-size: 20px;
			vertical-align: middle;
			top: 0;
		}
		&--bg {
			background-color: $primary;
			color: #fff;
			&.bg-grey-dark {
				color: $color;
			}
			.icon {
				font-size: 14px;
				top: 1px;
			}
		}
		&--small {
			.icon {
				font-size: 14px;
			}
		}
	}
	a#{&} &__label {
		text-decoration: underline;
	}
	a#{&} &__icon:not(&__icon--bg) {
		color: $primary;
	}
	&--after {
		padding-left: 0;
		padding-right: 39px;
	}
	&--after &__icon {
		left: auto;
		right: 0;
	}
}
