$s: '.menu-submenu';

.menu-submenu{
	z-index: 50;
	font-size: 16px;
	text-transform: none;
	font-family: $font;
	visibility: hidden;
	opacity: 0;
	display: none;
	transition: opacity .2s;
	margin: 0 -15px;
	ul,
	> ul:first-child{
		margin: 0;
	}
	&__list{
		@extend %reset-ul;
		padding: 9px 0 10px 20px;
	}
	&__item{
		@extend %reset-ul-li;
	}
	&__link{
		display: block;
		padding: 6px 18px;
		text-decoration: none;
		position: relative;
		&:before{
			@extend %icon;
			color: #AAAAAA;
			content: $fa-var-angle-right;
			position: absolute;
			left: 0;
			top: 8px;
			font-size: 18px;
		}
		&.is-active{
			color: $dark;
		}
		&--external{
			.icon {
				font-size: 12px;
				color: #AAAAAA;
				position: relative;
				top: -2px;
			}
		}
	}
	@media (min-width: $tablet){
		position: absolute;
		left: -2px;
		top: 100%;
		min-width: 380px;
		margin: 2px 0 0;
		background: #FFF;
		border: 2px solid #EEEEEE;
		border-radius: 2px;
		&__list{
			padding: 9px 14px 14px 20px;
		}
	}
}
