$s: '.message';

.message{
	margin: 0 0 20px;
	background: #fff;
	border: 1px solid $border;
	position: relative;
	text-align: center;
	line-height: 1.2;
	padding: 20px 20px 15px;
	p:last-child{
		margin-bottom: 0;
	}
	&__close{
		position: absolute;
		right: 10px;
		top: 10px;
		font-size: 20px;
		color: $input;
		@include hover() {
			&:hover {
				color: $muni;
				text-decoration: none;
			}
		}
	}
	&__desc{
		max-width: 370px;
		margin: 0 auto 20px;
	}
	&__icon{
		font-size: 28px;
		vertical-align: top;
		margin: 0 0 20px;
	}
	&__title{
		color: $dark;
		margin: 0 0 5px;
		font-size: 24px;
		font-family: $fontMuni;
	}
	&__link-close{
		font-size: 14px;
		font-weight: bold;
		text-transform: uppercase;
		a{
			color: $color;
		}
	}
	&--common{
		color: #fff;
		background: $muni;
		#{$s}__close,
		#{$s}__link-close a{
			color: #fff;
			@include hover() {
				&:hover {
					color: $input;
				}
			}
		}
		#{$s}__icon,
		#{$s}__title{
			color: #fff;
		}
		&-error{
			background: $red;
		}
		&-success{
			background: $green;
		}
		&-warning{
			background: $orange;
		}
		&-info{
			background: $muni;
		}
	}
	&--error{
		#{$s}__icon{
			color: $red;
		}
	}
	&--success{
		#{$s}__icon{
			color: $green;
		}
	}
	&--warning{
		#{$s}__icon{
			color: $orange;
		}
	}
	&--info{
		#{$s}__icon{
			color: $blue;
		}
	}

	@media(min-width: $tablet){
		padding: 24px 60px 17px;
	}
}

