.box-fixed-notice{
	font-size: 14px;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	z-index: 1000;
	background: $muni;
	color: #fff;
	padding: 14px 0;
	text-align: center;
	&--old-browsers{
		display: none;
		.old-browser &{
			display: block;
		}
	}
	a{
		color: #fff;
	}
	&__text{
		margin: 10px;
		&:first-child {
			margin-top: 10px;
		}
	}
	&__btn-wrap{
		margin: 0;
	}

	@media (min-width: $tablet){
		padding: 14px 0;
		text-align: left;
		.row-main{
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 0 25px;
		}
		&__text{
			flex: 1 1;
			margin-left: 0;
		}
	}
}
