.#{$fa-css-prefix},
.fas,
.far,
.fal,
.fab,
.icon {
  font-family: 'Font Awesome Pro', 'Font Awesome Brands';
  font-weight: 300;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
  text-decoration: none;
}

%icon {
  @include fa-icon;
}
