$s: '.box-timeline';

.box-timeline{
	margin: 0 0 $mMarginSmall;
	//overflow: hidden;
	p:last-child{
		margin-bottom: 0;
	}
	&__list{
		@extend %reset-ul;
		// overflow: hidden;
		position: relative;
		&:before{
			content: '';
			position: absolute;
			left: 9px;
			top: 0;
			bottom: 0;
			width: 1px;
			background: $border;
		}
		+ *{
			margin-top: $mMargin;
		}
	}
	&__item{
		@extend %reset-ul-li;
		margin-top: $mMarginSmall;
		overflow: hidden;
		&:first-child,
		&:last-child{
			#{$s}__point{
				&:before{
					content: '';
					position: absolute;
					left: 50%;
					width: 2px;
					height: 5000px;
					margin-left: -1px;
					background: $background;
				}
			}
		}
		&:first-child{
			#{$s}__point{
				&:before{
					bottom: 100%;
					margin-bottom: 3px;
				}
			}
		}
		&:last-child{
			#{$s}__point{
				&:before{
					top: 100%;
					margin-top: 1px;
				}
			}
		}
		&--end{
			#{$s}__point{
				background: $background;
				border: 1px solid $border;
				width: 13px;
				height: 13px;
				left: 5px;
				top: 5px;
			}
		}
	}
	&__inner{
		display: block;
		color: $color;
		text-decoration: none;
		padding: 0 0 0 35px;
		position: relative;
		&[href]{
			#{$s}__title{
				color: $link;
			}
			@include hover() {
				&:hover {
					#{$s}__title{
						color: $linkHover;
					}
				}
			}
			&:visited{
				#{$s}__title{
					color: $linkVisited;
				}
			}
			@include hover() {
				&:visited:hover {
					#{$s}__title{
						color: $linkHover;
					}
				}
			}
		}
	}
	&__point{
		position: absolute;
		left: 2px;
		top: 4px;
		border: 3px solid $color;
		width: 19px;
		height: 19px;
		border-radius: 50%;
		background: $color;
	}
	&__title{
		display: block;
		font-size: 16px;
		color: $dark;
		font-family: $fontSecondary;
		transition: color .2s;
		-moz-transition: none;
	}
	&__date{
		display: block;
		font-size: 16px;
	}
	&__title-date{
		color: #595959;
		font-size: 16px;
		margin: 0 0 15px 25px;
		padding-top: 20px;
		&--big{
			font-weight: bold;
			font-size: 24px;
		}
	}
	&__box{
		border: 1px solid $border;
		background: #fff;
		padding: 15px;
		position: relative;
		&:before,
		&:after{
			content: '';
			position: absolute;
			right: 100%;
			top: 16px;
			border-width: 10px 10px 10px 0;
			border-style: dashed solid;
			border-color: transparent $border;
		}
		&:after{
			margin-right: -2px;
			border-color: transparent #fff;
		}
		&__title{
			font-size: 18px;
			margin-top: 0;
			&--noborder{
				padding-bottom: 0 !important;
				&:before{
					display: none;
				}
			}
			&--big{
				font-size: 24px;
			}
		}
		&__img {
			position: absolute;
			top: -1px;
			bottom: -1px;
			right: -1px;
			overflow: hidden;
			margin-bottom: 0;
			img {
				position: absolute;
				top: 50%;
				left: 50%;
				max-width: none;
				min-width: 100%;
				min-height: 100%;
				width: auto;
				height: auto;
				transform: translate(-50%, -50%);
			}
		}
		#{$s}__point{
			top: 15px;
			left: -36px;
		}
	}

	&--center{
		text-align: center;
		#{$s}__list{
			font-size: 18px;
			font-family: $fontSecondary;
			font-weight: bold;
			&:before{
				left: 50%;
			}
			+ *{
				margin-top: $mMargin;
			}
		}
		#{$s}__item{
			margin-top: $mMargin;
			&--end{
				font-weight: normal;
				#{$s}__title,
				#{$s}__date{
					color: $color;
				}
				#{$s}__point{
					top: 8px;
					margin-left: -6px;
				}
			}
		}
		#{$s}__inner{
			padding: 0 50% 0 0;
			text-align: right;
			&[href]{
				color: $link;
				#{$s}__title,
				#{$s}__date {
					color: inherit;
				}
				@include hover() {
					&:hover {
						color: $dark;
					}
				}
				@include hover() {
					&:hover #{$s}__title{
						color: inherit;
					}
				}
				&:visited{
					#{$s}__title,
					#{$s}__date{
						color: $linkVisited;
					}
				}
				@include hover() {
					&:visited:hover {
						#{$s}__title,
						#{$s}__date{
							color: $linkHover;
						}
					}
				}
			}
		}
		#{$s}__point{
			top: 4px;
			left: 50%;
			margin-left: -9px;
		}
		#{$s}__title,
		#{$s}__date{
			font-size: 100%;
			color: $dark;
			-moz-transition: none;
		}
		#{$s}__title{
			padding-right: 25px;
		}
		#{$s}__date{
			position: absolute;
			left: 50%;
			top: 0;
			width: 50%;
			padding-left: 25px;
			text-align: left;
		}

	}

	&--boxes{
		font-size: 16px;
		#{$s}__list{
			margin: 0 -10px 0 0;
			padding: 0 10px 0 0;
		}
		#{$s}__box__title{
			position: relative;
			padding-bottom: 15px;
			&:before{
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				width: 40px;
				height: 2px;
				background: $color;
			}
		}
		#{$s}__inner{
			&[href]{
				-moz-transition: none;
				#{$s}__box{
					&__title{
						color: $link;
						transition: color .2s;
						-moz-transition: none;
					}
					&--bg {
						#{$s}__box {
							&__title {
								color: #fff;
							}
						}
					}
				}
				&:visited{
					#{$s}__box{
						&__title{
							color: $linkVisited;
						}
					}
				}
				@include hover() {
					&:hover {
						color: $color;
						#{$s}__box{
							&__title{
								color: $dark;
							}
							&--bg {
								#{$s}__box {
									&__title {
										color: #fff;
									}
								}
							}
						}
					}
				}
			}
		}
		#{$s}__item{
			&:last-child{
				#{$s}__point{
					&:before{
						margin-top: 3px;
					}
				}
			}
		}
	}

	&--half{
		max-width: 542px;
	}

	&--two-third{
		max-width: 800px;
	}

	&--titles{
		#{$s}__box__title{
			font-family: $fontMuni;
		}
	}

	&--numbers{
		#{$s}__box{
			padding-bottom: 40px;
		}
		[data-number] {
			top: 14px;
			width: 26px;
			height: 26px;
			border-width: 2px;
			&:after{
				content: attr(data-number);
				font-size: 14px;
				font-family: $fontMuni;
				font-weight: bold;
				color: #fff;
				text-align: center;
				display: block;
				width: 22px;
				height: 22px;
				position: relative;
				top: 2px;
			}
		}
		#{$s}{
			margin-bottom: 0;
		}
		#{$s}__point{
			left: -36px;
		}
		#{$s}__list {
			&:before{
				left: 12px;
			}
			#{$s}{
				display: inline-block;
				vertical-align: middle;
				width: auto;
			}
			#{$s}__list{
				&:before{
					left: 9px;
				}
				#{$s}__box{
					padding-bottom: 25px;
					margin-left: 0;
				}
				#{$s}__box__title{
					padding-bottom: 0;
					&:before{
						display: none;
					}
				}
				#{$s}__item{
					#{$s}__point{
						left: -36px;
					}
					&:first-child{
						#{$s}__point{
							&:before{
								bottom: 100%;
								margin-bottom: 3px;
								top: auto;
								margin-top: 0;
								background: #fff;
							}
						}
					}
					&:last-child{
						#{$s}__point{
							&:before{
								top: 100%;
								margin-top: 3px;
								bottom: auto;
								margin-bottom: 0;
								background: #fff;
							}
						}
					}
				}
			}
		}
	}

	&__box{
		&--bg {
			background: $muni;
			color: $whiteInvertable;
			&:after {
				border-color: transparent $muni;
			}
			#{$s}__box {
				&__title {
					color: $whiteInvertable;
					&:before{
						background: #fff;
					}
				}
			}
		}
	}

	@media (max-width: $tablet - 1) {
		&__box {
			&__img {
				display: none;
			}
		}
	}

	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		&__list {
			&::before {
				left: 11px;
			}
		}
		&__inner{
			padding-left: 50px;
		}
		&__title-date{
			margin-left: 30px;
		}
		&__box{
			padding: 20px 30px 25px;
			&:before,
			&:after{
				border-width: 20px 20px 20px 0;
				top: 8px;
			}
			&__img {
				width: 220px;
			}
			#{$s}__point{
				top: 41px;
				left: -49px;
			}
			&--img {
				padding-right: 250px;
			}
		}

		&--center{
			#{$s}__item{
				&--end{
					font-size: 18px;
				}
			}
			#{$s}__title{
				padding-right: 40px;
			}
			#{$s}__date{
				padding-left: 40px;
			}

		}

		&--boxes{
			#{$s}__point{
				top: 17px;
			}
		}

		&--numbers{
			[data-number] {
				top: 8px;
				width: 40px;
				height: 40px;
				border-width: 2px;
				&:after{
					content: attr(data-number);
					font-size: 18px;
					font-family: $fontMuni;
					font-weight: bold;
					color: #fff;
					text-align: center;
					display: block;
					width: 36px;
					height: 36px;
					position: relative;
					top: 5px;
				}
			}
			#{$s}__list {
				&::before{
					left: 19px;
				}
				#{$s}__list{
					&::before {
						left: 11px;
					}
					#{$s}__item{
						#{$s}__point{
							left: -49px;
						}
					}
				}
			}
			#{$s}__box{
				margin-left: 15px;
			}
			#{$s}__point{
				left: -66px;
			}
		}
	}

	@media (min-width: $desktop) {
		&__box {
			&__img {
				width: 370px;
			}
			&--img {
				padding-right: 400px;
			}
		}
	}
}
