.menu-mobile{
	&__list{
		@extend %reset-ul;
		display: flex;
		align-items: center;
		margin-left: -12px;
		font-size: 18px;
	}
	&__item{
		@extend %reset-ul-li;
		margin-left: 12px;
	}
	&__link{
		display: block;
		text-decoration: none;
	}
	&__burger{
		display: block;
		width: 16px;
		height: 16px;
		position: relative;
		&:before,
		&:after{
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			height: 2px;
			background: $muni;
			transition: transform .2s;
		}
		&:before{
			transform-origin: 0% 0%;
			top: 0;
		}
		&:after{
			transform-origin: 0% 100%;
			bottom: 0;
		}
		> span{
			position: absolute;
			right: 0;
			width: 12px;
			height: 2px;
			top: 50%;
			margin-top: -1px;
			background: $muni;
		}
		.is-open &{
			&:before{
				width: 20px;
				transform: rotate(45deg);
			}
			&:after{
				width: 20px;
				transform: rotate(-45deg);
			}
			> span{
				opacity: 0;
			}
		}
	}

	@media (min-width: $tablet){
		display: none;
	}
}
