$s: '.box-event-detail';

.box-event-detail {
	margin: 0 0 $mMarginSmall;
	&__title {
		font-size: 30px;
		margin-bottom: 20px;
	}
	p, ul, ol {
		font-size: 16px;
	}
	&__btn {
		margin: 2.1875em 0 0;
	}
	&__info{
		font-weight: bold;
		border: 2px solid $color;
		border-width: 2px 0;
		margin-bottom: 20px;
		@extend %reset-ul;
		li{
			@extend %reset-ul-li;
			border-bottom: 2px solid $color;
			margin-bottom: 0;
			padding: 13px 0;
			&:last-child{
				border-bottom: 0;
			}
		}
	}
	@media (min-width: $tablet) {
		margin: 0 0 $mMargin;
	}
}
