.menu-header-secondary{
	font-size: 18px;
	display: flex;
	align-items: center;
	@extend %reset-ul;
	&__item{
		@extend %reset-ul-li;
	}
	&__link{
		white-space: nowrap;
		text-decoration: none;
		padding: 10px 0 12px;
		display: block;
		&.is-active{
			color: $dark;
		}
	}
	&--left{
		align-items: flex-end;
	}
	@media(min-width: $tablet){
		&__item {
			margin-left: 30px;
		}
	}
}
