$s: '.menu-filter';

.menu-filter{
	margin: 0 0 $mMarginSmall;
	text-align: center;
	.box-tabs &{
		margin: 0;
	}
	&__list{
		@extend %reset-ul;
		display: table;
		width: 100%;
	}
	&__item{
		@extend %reset-ul-li;
		display: table-cell;
		vertical-align: middle;
		&:first-child{
			#{$s}__link{
				border-left-width: 1px;
			}
		}
	}
	&__link{
		border-bottom: 2px solid $border;
		display: block;
		text-decoration: none;
		padding: 15px 10px;
		transition: color .2s, background .2s, border-color .2s;
		&.is-active{
			font-weight: bold;
			border-color: $color;
			color: $color;
			@include hover() {
				&:hover {
					border-color: $color;
					color: $color;
				}
			}
		}
	}

	&--big{
		#{$s}__link{
			padding-top: 20px;
			padding-bottom: 20px;
		}
	}
	@media (min-width: $tablet) {
		margin: 0 0 $mMargin;
	}
	@media(min-width: $desktop) {
		&__list{
			table-layout: fixed;
		}
		&__link{
			padding-left: 15px;
			padding-right: 15px;
		}
	}
}
