$s: '.menu-primary';

.menu-primary{
	position: relative;
	z-index: 200;
	a {
		-webkit-tap-highlight-color: transparent;
	}
	header:not(.header--hero--noline) &__holder{
		border-bottom: 2px solid #EEEEEE;
	}
	&__wrap{
		.has-toggle &{
			padding-right: 100px;
		}
	}
	&__list{
		@extend %reset-ul;
		font-size: 18px;
	}
	&__item{
		@extend %reset-ul-li;
		&.with-submenu,
		&.with-subbox{
			#{$s}__link{
				padding-right: 15px;
				cursor: pointer;
				&:after{
					content: '';
					position: absolute;
					left: -20px;
					right: -20px;
					margin-top: -1px;
					top: 100%;
					height: 6px;
					z-index: 100;
					background: #fff;
					opacity: 0;
					visibility: hidden;
				}
			}
			&.is-open{
				#{$s}__link{
					.icon{
						transform: rotate(180deg);
					}
				}
			}
		}
		&.with-submenu {
			position: relative;
		}
		&.with-submenu,
		&.with-subbox{
			&.is-open{
				.menu-submenu,
				.menu-subbox{
					opacity: 1;
					visibility: visible;
					display: block;
				}
			}
		}
	}
	&__link{
		color: $muni;
		display: block;
		text-decoration: none;
		padding: 10px 0;
		position: relative;
		transition: none;
		.icon{
			position: absolute;
			right: 0;
			top: 50%;
			font-size: 24px;
			margin-top: -10px;
		}
	}
	&__inner{
		&:before{
			content: '';
			position: absolute;
			bottom: -4px;
			left: 0;
			right: 0;
			height: 2px;
			background: $color;
			opacity: 0;
		}
	}
	&__toggle{
		display: none;
	}
	// main menu toggle
	&__title{
		display: none;
		position: absolute;
		top: -36px;
		font-size: 0;
		right: 15px;
		text-transform: uppercase;
		font-family: $fontSecondary;
		font-weight: bold;
		text-decoration: none;
		padding-left: 16px;
		width: 16px;
		height: 16px;
		z-index: 1;
		&:before,
		&:after{
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			height: 2px;
			background: $muni;
			transition: transform .2s;
		}
		&:before{
			transform-origin: 0% 0%;
			top: 0;
		}
		&:after{
			transform-origin: 0% 100%;
			bottom: 0;
		}
		> span{
			position: absolute;
			right: 0;
			width: 12px;
			height: 2px;
			top: 50%;
			margin-top: -1px;
			background: $muni;
		}
		@include hover() {
			&:hover {
				color: $muni;
			}
		}
		.has-toggle &{
			display: block;
		}
		.is-open &{
			&:before{
				width: 20px;
				transform: rotate(45deg);
			}
			&:after{
				width: 20px;
				transform: rotate(-45deg);
			}
			> span{
				opacity: 0;
			}
		}
	}
	&__main-toggle{
		position: absolute;
		left: 0;
		right: 0;
		top: 100%;
		z-index: 102;
		visibility: hidden;
		opacity: 0;
		overflow: hidden;
		background: #fff;
		> .row-main{
			display: block;
		}
		.header & .grid__cell{
			.grid {
				margin-bottom: 0;
				&__cell {
					margin-bottom: 0;
				}
			}
		}
		#{$s}__title{
			display: none;
		}
		#{$s},
		.menu-header-secondary,
		.menu-submenu{
			&__link{
				color: $muni;
				text-decoration: none;
				@include hover() {
					&:hover {
						color: $linkHover;
					}
				}
				.header--hero &{
					color: $muni;
					&.is-active{
						color: $linkHover;
					}
					@include hover() {
						&:hover {
							color: $linkHover;
						}
					}
				}
			}
		}
		.menu-submenu{
			&__link{
				.header--hero &{
					@include hover() {
						&:hover {
							color: #fff;
						}
					}
				}
			}
		}
		#{$s}{
			font-size: 16px;
			font-family: $fontSecondary;
			&__list{
				display: block;
				margin-left: 0;
			}
			&__item{
				padding: 0 20px;
				.has-toggle & {
					border-width: 0 0 2px 0;
					border-bottom-color: #EEEEEE;
				}
				&.with-subbox.is-open{
					background: $greyLight;
				}
				&.with-submenu.is-open,
				&.with-subbox.is-open{
					position: relative;
					#{$s}__link{
						font-weight: bold;
						color: $color;
					}
					#{$s}__toggle{
						&:before{
							content: $fa-var-angle-up;
						}
					}
					.menu-submenu{
						display: block;
					}
					.menu-subbox{
						transform: translateX(0);
						visibility: visible;
						transition-delay: 0s, 0s;
					}
				}
			}
			&__links{
				position: relative;
				display: block;
				padding-right: 50px;
			}
			&__link{
				display: block;
				padding: 13px 15px 13px 0;
				.icon{
					display: none;
				}
				&:after{
					display: none;
				}
			}
			&__inner{
				&:before{
					display: none;
				}
			}
			&__toggle{
				display: block;
				position: absolute;
				right: 0;
				top: 0;
				font-size: 0px;
				height: 50px;
				line-height: 50px;
				width: 50px;
				color: $muni;
				text-align: right;
				&:after{
					content: '';
					position: absolute;
					right: 100%;
					top: 0;
					bottom: 0;
					width: 800px;
				}
				@include hover() {
					&:hover {
						color: $muni;
					}
				}
				&:before{
					display: inline-block;
					vertical-align: middle;
					font-size: 28px;
				}
			}
		}
		.menu-header-secondary{
			background: #F8F8FD;
			font-size: 14px;
			&__link{
				display: block;
				padding: 14px 18px;
			}
			&__item {
				margin: 0;
			}
		}
		.menu-submenu{
			display: none;
			position: static;
			width: auto;
			border: none;
			.has-toggle & {

			}
		}
		.menu-subbox{
			z-index: 1;
			padding: 0 0 10px;
			border: none;
			.has-toggle & {
				position: static;
				background: transparent;
			}
			&__title{
				padding: 13px 0;
				margin: 0;
				&__link{
					text-decoration: none;
					display: block;
				}
			}
			&__list{
				margin: 0;
			}
		}
		.menu-header-secondary{
			display: block;
			position: relative;
			font-size: 16px;
			margin-top: -2px;
			border-bottom: 2px solid $greyDark;
			&__link{
				border-top: 2px solid rgba(#0000DC, .2);
			}
		}
	}
	// STATES
	&.is-open{
		.menu-header-secondary{
			visibility: visible;
		}
		#{$s}{
			&__main-toggle{
				visibility: visible;
				opacity: 1;
			}
			&__title{
				.icon{
					&:before{
						display: none;
					}
					&:after{
						content: $fa-var-times;
					}
				}
			}
		}
	}

	// mobile
	@media(max-width: $tablet - 1){
		&__link{
			font-size: 16px;
			padding: 15px 0;
		}
		&__toggle{
			display: block;
		}
		&__title{
			display: none !important;
		}
		&__main-toggle{
			transition: none !important;
			> .row-main{
				padding: 0;
			}
			#{$s}{
				&__toggle {
					padding-right: 20px;
					margin-right: -20px;
				}
			}
		}
		&__item {
			border-bottom: 2px solid #EEEEEE;
		}
	}

	@media(min-width: $tablet){
		display: block;
		visibility: visible;
		opacity: 1;
		&__wrap {
			display: flex;
			justify-content: space-between;
			align-items: flex-end;
		}
		&__list{
			display: flex;
			flex: 1 0 auto;
			margin-left: -20px;
		}
		&__item{
			padding: 0 20px;
			margin: 0;
			border: 2px solid transparent;
			border-radius: 2px 2px 0 0;
			&.with-submenu,
			&.with-subbox {
				&.is-open{
					background: #fff;
					border-color: #EEEEEE;
					#{$s}__link{
						&:before,
						&:after{
							opacity: 1;
							visibility: visible;
						}
					}
				}
			}
		}
		&__link{
			font-weight: 400;
			&.is-active{
				font-weight: bold;
				color: $dark;
				#{$s}__inner{
					&:before{
						background: currentColor;
						opacity: 1;
					}
				}
			}
			.icon{
				font-size: 16px;
				margin-top: -7px;
			}
		}
		&__main-toggle{
			width: 500px;
			left: auto;
			right: 10px;
			border: 2px solid #EEEEEE;
			border-width: 0 2px;
			border-radius: 2px;
			> .row-main{
				display: block;
				padding: 0;
			}
			.grid__cell{
				width: 100%;
			}
			#{$s}__item {
				padding: 0 15px;
			}
			.menu-header-secondary{
				border-bottom: 2px solid #EEEEEE;
			}
		}
		&__title{
			right: 25px;
			top: 20px;
		}
	}
}
