$s: '.header-min';

.header-min{
	background: $muni;
	line-height: 40px;
	font-size: 14px;
	font-family: $font;
	-webkit-font-smoothing: subpixel-antialiased;
	// logo
	&__logo{
		font-size: 0;
		float: left;
		svg{
			vertical-align: middle;
			width: 259px;
		}
		path{
			fill: #fff;
		}
	}

	// menu
	&__menu{
		float: right;
		&__list{
			@include clearfix();
			@extend %reset-ul;
		}
		&__item{
			float: left;
			@extend %reset-ul-li;
			margin-left: 20px;
			&--lang{
				margin-left: 30px;
			}
		}
		&__link{
			color: #fff;
			text-decoration: none;
			-moz-transition: none;
			@include hover() {
				&:hover,
				&:visited:hover {
					text-decoration: underline;
					color: #fff;
				}
			}
		}
	}

	// inverse
	&--inverse{
		background: #fff;
		color: $muni;
		#{$s}{
			&__logo{
				path{
					fill: $muni;
				}
			}
			&__menu{
				&__link{
					color: $muni;
					@include hover() {
						&:hover {
							color: $linkHover;
						}
					}
				}
			}
		}
	}

	// dark
	&--dark{
		background: #000;
		color: #fff;
		#{$s}{
			&__logo{
				path{
					fill: #fff;
				}
			}
			&__menu{
				&__link{
					color: #fff;
					-moz-transition: none;
					@include hover() {
						&:hover,
						&:visited:hover {
							color: #fff;
						}
					}
				}
			}
		}
	}

	// faculty
	&-law{
		background: $law;
	}
	&-med{
		background: $med;
	}
	&-sci{
		background: $sci;
	}
	&-fi{
		background: $fi;
		color: $color;
		#{$s}{
			&__logo{
				path{
					fill: $color;
				}
			}
			&__menu{
				&__link{
					color: $color;
					@include hover() {
						&:hover {
							color: $color;
						}
					}
				}
			}
		}
	}
	&-fss{
		background: $fss;
	}
	&-fsps{
		background: $fsps;
	}
	&-fss{
		background: $fss;
	}
	&-phil{
		background: $phil;
		color: $color;
		#{$s}{
			&__logo{
				path{
					fill: $color;
				}
			}
			&__menu{
				&__link{
					color: $color;
					@include hover() {
						&:hover {
							color: $color;
						}
					}
				}
			}
		}
	}
	&-ped{
		background: $ped;
		color: $color;
		#{$s}{
			&__logo{
				path{
					fill: $color;
				}
			}
			&__menu{
				&__link{
					color: $color;
					@include hover() {
						&:hover {
							color: $color;
						}
					}
				}
			}
		}
	}
	&-econ{
		background: $econ;
	}
	&-pharm{
		background: $pharm;
	}

	+ .header{
		border-top: none;
	}

	@media(max-width: $tablet - 1){
		&__menu{
			&__item{
				margin-left: 10px;
			}
		}
	}
}
