$s: '.footer-light';

.footer-light{
	padding: 50px 0 40px;
	text-align: center;
	background: #fff;
	color: $muni;
	path{
		fill: $muni;
	}
	a{
		-moz-transition: none;
		&:visited{
			color: $linkVisited;
		}
		@include hover() {
			&:visited:hover {
				color: $linkHover;
			}
		}
	}

	&__copyrights{
		font-size: 14px;
		margin-bottom: 20px;
		&.with-img{
			#{$s}__copyrights__img{
				display: block;
				margin: 0 0 40px;
			}
		}
	}

	&__links{
		font-size: 14px;
		color: $muni;
		margin-bottom: 0;
		a{
			font-weight: bold;
			margin: 0 2px;
		}
	}

	&--dark{
		background: $color;
		color: #fff;
		border: none;
		#{$s}__menu,
		#{$s}__links{
			border-color: #fff;
		}
		a{
			-moz-transition: none;
			color: #fff;
			&:visited{
				color: #fff;
			}
			@include hover() {
				&:hover {
					color: $light;
				}
			}
			@include hover() {
				&:visited:hover {
					color: $light;
				}
			}
		}
		path{
			fill: #fff;
		}
		#{$s}__links{
			color: #fff;
		}
	}
	&--blue{
		background: $muni;
	}

	@media(min-width: $tablet){
		text-align: left;
		padding: 40px 0;
		&__links{
			font-size: 16px;
		}
		&__copyrights{
			margin-bottom: 15px;
			&.with-img{
				padding-left: 132px;
				position: relative;
				#{$s}__copyrights__img{
					position: absolute;
					left: 0;
					top: 50%;
					margin: -17px 0 0;
				}
				+ #{$s}__links{
					padding-left: 132px;
				}
			}
		}
	}

	@media(min-width: $desktop){
		p{
			margin-bottom: 0;
		}
		&__wrap{
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		&__copyrights{
			&.with-img{
				+ #{$s}__links{
					padding-left: 0;
				}
			}
		}
	}
}
