$s: '.box-timeline-progress';

.box-timeline-progress {
	margin: 0 0 $mMarginSmall;
	text-align: center;
	overflow: hidden;

	&__list {
		@extend %reset-ol;
		@include inline-list(33.333%, $fontSizeSmall);
		margin-left: -30px;
	}
	&__item {
		@extend %reset-ol-li;
		overflow: hidden;
		box-sizing: border-box;
		padding-left: 30px;
		line-height: (22 / 14);
		&:first-child {
			#{$s}__illust:before {
				display: none;
			}
		}
		&:last-child {
			#{$s}__illust:after {
				display: none;
			}
		}
		&--ok {
			#{$s}__illust {
				color: $green;
				border-color: $green;
			}
		}
		&--active {
			#{$s}__illust {
				border-color: $muni;
			}
		}
	}
	&__illust {
		font-family: $fontMuni;
		position: relative;
		display: inline-block;
		vertical-align: top;
		width: 72px;
		height: 72px;
		margin-bottom: 0;
		border: 3px solid $border;
		text-align: center;
		font-size: 32px;
		line-height: 66px;
		font-weight: bold;
		color: $muni;
		border-radius: 40px;
		&:before,
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			height: 1px;
			width: 1000px;
			background: #aaa;
		}
		&:before {
			right: 100%;
			margin-right: 7px;
		}
		&:after {
			left: 100%;
			margin-left: 7px;
		}

		.icon {
			top: -2px;
			font-size: 21px;
			&-check {
				top: 2px;
				font-size: 30px;
			}
			&-calendar-plus-o{
				top: -3px;
			}
		}
	}
	&__title {
		@extend .h4;
		margin-top: 30px;
		margin-bottom: .3em;
	}

	&--reverse{
		#{$s}__item{
			&--ok{
				#{$s}__illust{
					color: #fff;
					background: $green;
				}
			}
			&--active{
				#{$s}__illust{
					color: #fff;
					background: $muni;
				}
			}
		}
	}

	@media(max-width: $tablet - 1){
		&__illust{
			width: 60px;
			line-height: 55px;
			font-size: 24px;
			height: 60px;
			border-width: 3px;
			&:before{
				margin-right: 3px;
			}
			&:after{
				margin-left: 3px;
			}
			.icon{
				font-size: 18px;
				&-check{
					font-size: 24px;
				}
			}
		}
		&__title,
		&__desc{
			display: none;
		}
	}
	@media (min-width: $tablet) {
		margin: 0 0 $mMargin;
	}
	@media(min-width: $desktop){
		&__desc {
			font-size: 18px;
			padding: 0 30px;
		}
	}
}
