.form-std{
	margin: 0 0 $mMarginSmall;
	&__fieldset{
		background: #fff;
		border: 1px solid $border;
		padding: 25px 15px 5px;
		margin: 0 0 $mMarginSmall;
	}
	&__legend{
		font-family: $fontSecondary;
		font-size: 24px;
		font-weight: bold;
		padding: 0 10px;
		color: $dark;
	}
	&__title{
		margin-bottom: 18px;
	}

	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		&__fieldset{
			padding: 60px 70px 25px;
		}
		&__legend{
			padding: 0 20px;
		}
	}
}
