$s: '.box-image-grid';

.box-image-grid{
	margin: 0 0 $mMarginSmall;
	&__main{
		display: block;
		background: $muni;
		padding: 200px 15px 20px 15px;
		position: relative;
		color: #fff;
		text-decoration: none;
		height: 100%;
		margin-bottom: 20px;
		p{
			font-size: 22px;
			line-height: 36px;
			margin-bottom: 50px;
		}
		p:last-child{
			margin-bottom: 0;
		}
		@include hover() {
			&:hover {
				color: #fff;
			}
		}
	}
	&__logo{
		position: absolute;
		left: 15px;
		top: 70px;
		width: 65px;
		height: 114px;
		img{
			width: 100%;
			height: auto;
		}
	}
	&__title{
		color: #fff;
		font-family: $fontMuni;
	}
	&__more{
		display: block;
		border: 1px solid $border;
		background: #fff;
		padding: 20px 15px 25px;
		margin-bottom: 20px;
 	}
	&__text{
		font-family: $fontMuni;
		font-weight: bold;
		font-size: 16px;
	}
	&__img{
		margin-bottom: 0;
		background-size: cover;
		background-repeat: no-repeat;
	}

	@media (max-width: $tablet){
		&__text{
			display: block;
			margin-top: 20px;
		}
		&__img{
			height: 250px;
		}
	}

	@media (min-width: $tablet){
		margin: 0 0 $mMargin;
		&__main{
			padding: 270px 30px 70px 50px;
			margin-bottom: 0;
		}
		p{
			font-size: 22px;
			line-height: 36px;
			margin-bottom: 50px;
		}
		p:last-child{
			margin-bottom: 0;
		}
		@include hover() {
			&:hover {
				color: #fff;
			}
		}
		&__logo{
			left: 50px;
		}
		&__more{
			padding: 45px 30px 35px;
		}
		.grid{
			display: flex;
		}
		.grid__cell{
			display: flex;
			flex-direction: column;
			flex: 1 1 50%;
			max-width: 100%;
		}
		&__link{
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		&__img{
			display: flex;
			flex: 1 1 auto;
		}
	}

	@media (min-width: $desktopWide){
		&__more{
			margin-bottom: 30px;
		}
	}
}
