$s: '.js-scrollbar';

.js-scrollbar {
	position: relative;
	.viewport {
		overflow: hidden;
		position: relative;
	}
	.overview {
		position: relative;
		top: 0;
		left: 0;
		right: 0;
		> *:last-child {
			margin-bottom: 0;
		}
	}
	.scrollbar {
		position: absolute;
		top: 0;
		right: 0;
		width: 8px;
		height: 8px;
		z-index: 1;
		.noSelect & {
			.thumb {
				background: $linkHover;
			}
		}
		@include hover() {
			&:hover {
				.thumb {
					background: $linkHover;
				}
			}
		}

	}
	.track {
		background: #fff;
		position: relative;
		height: 6px;
		width: 6px;
	}
	.thumb {
		position: absolute;
		top: 0;
		right: 0;
		overflow: hidden;
		cursor: pointer;
		width: 6px;
		height: 6px;
		background: $primary;
		transition: background .2s, transform .2s;
	}

	&--x {
		.viewport {
			padding-bottom: 25px;
		}
		.scrollbar {
			top: auto;
			bottom: 0;
			.noSelect &{
				.thumb {
					transform: scaleY(1.35);
				}
			}
			@include hover() {
				&:hover {
					.thumb {
						transform: scaleY(1.35);
					}
				}
			}
		}
	}

	&--y {
		.viewport {
			height: 250px;
			padding-right: 25px;
		}
		.scrollbar {
			.noSelect &{
				.thumb {
					transform: scaleX(1.35);
				}
			}
			@include hover() {
				&:hover {
					.thumb {
						transform: scaleX(1.35);
					}
				}
			}
		}

	}

	&--bordered {
		border: 1px solid $greyDark;
		background: #fff;
		.track {
			background: $greyLight;
		}
		.scrollbar,
		.viewport {
			margin: 9px 7px 9px 9px;
		}
		&#{$s}--y {
			.viewport {
				margin-right: 28px;
				padding-right: 0;
			}
		}
		&#{$s}--x {
			.viewport {
				margin-bottom: 28px;
				padding-bottom: 0;
			}
		}
	}

	@media(min-width: $tablet){
		&--bordered {
			.scrollbar,
			.viewport {
				margin: 19px 17px 19px 19px;
			}
			&#{$s}--y {
				.viewport {
					margin-right: 48px;
				}
			}
			&#{$s}--x {
				.viewport {
					margin-bottom: 48px;
				}
			}
		}
	}
}
.noSelect {
	user-select: none;
}
