.table-vcard {
	border: 0;
	// width: auto;
	th,
	td {
		border: 0;
		padding: 0;
	}
	th {
		font-weight: normal;
	}
	td {
		font-weight: bold;
		color: $dark;
		padding-left: 10px;
		a[href*="mailto:"]{
			hyphens: manual;
			word-wrap: break-word;
		}
	}

	@media(max-width: $tablet - 1){
		table,
		tbody,
		tr,
		td{
			display: block;
			width: 100%;
		}
		th{
			display: none;
		}
		td{
			padding-left: 0;
		}
	}

	@media(min-width: $tablet){
		font-size: 16px;
	}
}
