$s: '.meta';

.meta{
	font-size: 0px;
	margin: 0 0 10px;
	-moz-transition: none;
	+ *{
		margin-top: 0;
	}
	a {
		&:visited{
			color: $linkVisited;
		}
		@include hover() {
			&:visited:hover {
				color: $linkHover;
			}
		}
	}
	&__item{
		padding: 0 5px;
		font-size: $fontSizeSmall;
		display: inline-block;
		vertical-align: middle;
		border-left: 1px solid $border;
		&:first-child{
			border-left-width: 0;
			padding-left: 0;
		}
	}
	.font-muni{
		position: relative;
		top: 2px;
	}

	&--block{
		position: relative;
		#{$s}__item{
			display: block;
			padding: 0;
			border-left: 0;
		}
	}

	&--inline{
		#{$s}__item{
			display: inline;
			padding: 0;
			border-left: 0;
			&:after{
				content: ',';
				display: inline-block;
				font-family: $font;
				padding-right: 6px;
			}
			&:last-child:after{
				content: '';
			}
		}
	}

	@media(min-width: $tablet){
		&__item{
			padding: 0 10px;
		}
	}
}
