$s: '.box-article-list';

.box-article-list{
	margin: 0 0 $mMarginSmall;
	display: block;
	position: relative;
	background: #fff;
	&__inner[href]{
		color: $color;
		text-decoration: none;
		@include hover() {
			&[href]:hover{
				color: $color;
				#{$s}__title{
					color: $linkHover;
					text-decoration: underline;
				}
				#{$s}__more{
					text-decoration: underline;
				}
			}
		}
		&:visited{
			#{$s}__title {
				color: $linkVisited;
			}
		}
		@include hover() {
			&:visited:hover {
				#{$s}__title {
					color: $linkHover;
				}
			}
		}
	}
	ul{
		margin: 0 0 1.5em;
	}
	&__content{
		border: 1px solid $border;
		border-width: 0px 1px 1px;
		padding: 15px;
		> :last-child{
			margin-bottom: 0;
		}
	}
	&__desc{
		font-size: 14px;
	}
	&__title{
		color: $link;
		font-size: 18px;
		margin: .5em 0;
		transition: color .2s;
		position: relative;
		padding-bottom: 15px;
		-moz-transition: none;
		&:before{
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 40px;
			height: 2px;
			background: $color;
		}
	}
	&__img{
		margin: 0;
	}

	&__more{
		font-size: 16px;
		font-weight: bold;
		font-family: $fontMuni;
		color: $muni;
	}

	&--noimg{
		#{$s}__content{
			border-width: 1px;
		}
	}

	&--border {
		#{$s}__content {
			padding: 40px 80px 40px 40px;
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: center;
			&:before,
			&:after {
				content: '';
				position: absolute;
				left: 40px;
				right: 80px;
				height: 2px;
				background: $color;
			}
			&:before{
				top: 35px;
			}
			&:after{
				bottom: 35px;
			}
		}
		#{$s}__meta{
			margin-bottom: 0;
		}
		#{$s}__title{
			margin-top: 5px;
			font-size: 30px;
			&:before{
				display: none;
			}
		}
	}

	&--reverse{
		background: $muni;
		color: #fff;
		#{$s}__meta,
		#{$s}__title,
		#{$s}__more{
			color: #fff;
			&:before{
				display: none;
			}
		}
		#{$s}__content{
			border-color: $muni;
		}
		#{$s}__inner{
			color: #fff;
			@include hover() {
				&[href]:hover{
					color: #fff;
					#{$s}__title{
						color: #fff;
					}
					#{$s}__more{
						color: #fff;
					}
				}
			}
			&[href]{
				&:visited{
					#{$s}__title {
						color: #fff;
					}
				}
			}
		}
	}

	&--img{
		#{$s}__inner{
			text-decoration: none;
		}
		#{$s}__content{
			background: rgba($color, .4);
			border-color: transparent;
			position: relative;
			z-index: 10;
		}
		#{$s}__img{
			left: 0;
		}
	}

	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		&__content{
			padding: 20px;
			width: 50%;
			border-width: 1px;
			min-height: 300px;
		}
		&__title{
			font-size: 24px;
		}
		&__img{
			position: absolute;
			left: 50%;
			top: 0;
			bottom: 0;
			right: 0;
			overflow: hidden;
			img{
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				max-width: none;
				min-height: 100%;
				width: auto;
			}
		}

		&--noimg{
			#{$s}__content{
				width: auto;
				min-height: 1px;
			}
		}
	}

	@media(min-width: $desktop){
		&__content{
			padding: 25px 60px 25px 40px;
		}
		&--big{
			font-size: 22px;
			.icon-ul > li{
				padding-left: 38px;
			}
			.icon-li{
				font-size: 24px;
				top: 50%;
			}
			#{$s}{
				&__title{
					font-size: 48px;
				}
				&__content{
					padding-top: 40px;
					padding-bottom: 40px;
					min-height: 500px;
				}
			}
		}
	}
}
