$s: '.box-deaf';

.box-deaf {
	&.is-open {
		#{$s} {
			&__link {
				opacity: 1;
				visibility: visible;
			}
			&__content {
				padding-left: 30px;
			}
		}
	}
	&__title{
		color: $muni;
	}
	&__toggle {
		font-size: 20px;
		line-height: 1;
		margin-left: 10px;
		color: $muni;
		position: relative;
		top: -5px;
	}
	&__content {
		position: relative;
		transition: padding-left .2s;
	}
	&__link {
		position: absolute;
		top: 0;
		left: 0;
		font-size: 20px;
		line-height: 1;
		color: $muni;
		visibility: hidden;
		opacity: 0;
		transition: opacity .2s;
	}

	@media (min-width: $tablet) {
		&.is-open {
			#{$s} {
				&__content {
					padding-left: 40px;
				}
			}
		}
		&__toggle {
			font-size: 28px;

		}
		&__link {
			font-size: 24px;
		}
	}

	@media (min-width: $desktop) {
		&__toggle {
			top: -10px;
		}
	}

	@media (min-width: $desktopWide + 120) {
		&.is-open {
			#{$s} {
				&__content {
					padding-left: 0;
				}
			}
		}
		&__link {
			font-size: 24px;
			left: -55px;
		}
	}
}
