$s: '.box-timeline-dates';

.box-timeline-dates {
	margin: 0 0 $mMarginSmall;
	.box-event {
		position: relative;
		z-index: 1;
	}
	&__box {
		padding-top: $mMarginSmall;
		border-top: 1px solid $border;
	}
	&__title {
		margin: 0 0 1.25em;
	}
	&__list {
		@extend %reset-ul;
		margin-bottom: -20px;
	}
	&__item {
		@extend %reset-ul-li;
		overflow: hidden;
		position: relative;
		&:last-child {
			&:after {
				display: none;
			}
		}
	}
	@media (min-width: $tablet) {
		margin: 0 0 $mMargin;
	}
	@media(min-width: $desktop){
		&__box{
			padding-top: $mMargin;
		}
		&__list{
			margin-bottom: 0;
		}
	}
}

