$s: '.box-vcards-list';

.box-vcards-list {
	margin: 0 0 $mMarginSmall;
	border: 2px solid $color;
	border-width: 2px 0;
	&__list{
		@extend %reset-ul;
	}
	&__item{
		@extend %reset-ul-li;
		position: relative;
		&:after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			height: 1px;
			background: $border;
		}
		&:last-child:after {
			display: none;
		}
		&--noimg {
			#{$s}__inner {
				padding-top: 0;
				padding-bottom: 0;
			}
			#{$s}__content {
				padding: 0;
			}
		}
		+ #{$s}__item {
			margin-top: -1px;
		}
	}
	&__inner {
		padding: 10px 15px;
		height: 90px;
		display: table;
		width: 100%;
		color: $color;
		text-decoration: none;
		@include inline-list(auto, $fontSize);
		transition: background .2s;
		@include hover() {
			&[href]:hover {
				color: $color;
				background: $elementsHover;
				#{$s}__name{
					color: $color;
				}
			}
		}
		> * {
			display: table-cell;
			vertical-align: middle;
		}
		&[href]{
			-moz-transition: none;
			#{$s}__name{
				color: $muni;
			}
			&:visited{
				#{$s}__name {
					color: $linkVisited;
				}
			}
			@include hover() {
				&:visited:hover {
					#{$s}__name{
						color: $linkHover;
					}
				}
			}
		}
	}
	&__img {
		margin: 0;
		width: 70px;
		img{
			max-width: none;
		}
	}
	&__name {
		font-size: 18px;
		margin: 0;
		-moz-transition: none;
		.icon {
			color: $muni;
		}
	}
	&__position {
		font-size: 14px;
		margin: 0;
	}
	&__content {
		padding: 10px 15px;
	}

	&--small {
		#{$s}__inner {
			height: 90px;
		}
	}

	@media(min-width: $tablet) {
		margin: 0 0 $mMargin;
		&--small {
			#{$s}__content {
				padding: 0 10px;
			}
		}
	}

	@media(min-width: $desktop){
		&__item{
			&--noimg {
				#{$s}__inner {
					padding: 12px 15px;
				}
			}
		}
		&__inner{
			padding: 15px;
		}
		&__content{
			padding-left: 25px;
			padding-right: 25px;
		}
		&__position{
			font-size: 18px;
		}

		&--small {
			#{$s}__inner {
				padding: 20px;
			}
		}
	}
}
