$s: '.menu-side';

.menu-side{
	margin: 0 0 $mMarginSmall;
	border-bottom: 1px solid $border;
	font-size: 18px;
	font-weight: bold;
	&__list{
		@extend %reset-ul;
		&--depth1 {
			> #{$s}__item.is-active {
				border: 2px solid black;
				border-width: 2px 0;
				position: relative;
				margin: -1px 0;
			}
		}
		&--depth1,
		&--depth2{
			> #{$s}__item{
				border-top: 1px solid $border;
				> #{$s}__link{
					padding: 12px 18px;
				}
			}
		}
		&--depth2{
			font-weight: normal;
			font-size: 16px;
			> #{$s}__item{
				> #{$s}__link{
					padding-left: 28px;
				}
			}
		}
		&--depth3{
			font-size: 14px;
			border: 1px solid darken($border, 5%);
			border-width: 1px 0;
			margin-bottom: -1px;
			position: relative;
			padding: 5px 0;
			> #{$s}__item{
				> #{$s}__link{
					padding: 5px 20px 5px 38px;
				}
			}
		}
		&--depth4{
			> #{$s}__item{
				> #{$s}__link{
					padding: 5px 20px 5px 58px;
				}
			}
		}
	}
	&__item{
		@extend %reset-ul-li;
		&.with-subitems{
			#{$s}__link{
				padding-right: 35px;
				position: relative;
			}
		}
		&.is-open {
			> #{$s}__list {
				display: block;
			}
			> #{$s}__link{
				font-weight: bold;
				color: $color;
			}
		}
		#{$s}__list {
			display: none;
		}
	}
	&__link{
		display: block;
		text-decoration: none;
		#{$s}__item.is-active > & {
			transition: opacity .2s, color .2s;
			color: #000;
		}
		&[href="#"] > .menu-side__toggle {
			width: auto;
			left: 0;
		}
	}
	&__toggle {
		width: 35px;
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		color: $muni;
		&:before {
			position: absolute;
			top: 50%;
			right: 12px;
			margin-top: -9px;
			#{$s}__item.is-open > #{$s}__link & {
				content: $fa-var-angle-up;
			}
		}
	}
	@media (min-width: $tablet) {
		margin: 0 0 $mMargin;
	}
}
