.menu-breadcrumb{
	margin: 0 0 $mMarginSmall;
	padding: 14px 0 15px;
	border-bottom: 1px solid rgba($border, .5);
	font-size: 0;
	background: #fff;
	+ .box-hero,
	+ .box-bg,
	+ .u-mt-0{
		margin-top: -30px !important;
	}
	+ .box-hero--bordered{
		margin-top: -31px;
	}
	&__list{
		@extend %reset-ul;
		display: flex;
		align-items: center;
	}
	&__item{
		@extend %reset-ul-li;
		font-size: 14px;
		padding: 0 25px 0 0;
		text-decoration: none;
		font-weight: 400;
		position: relative;
		.icon-angle-right{
			position: absolute;
			right: 10px;
			top: 50%;
			margin-top: -6px;
			font-size: 14px;
			color: $muni;
		}
		&--home {
			@include text-hide();
			a:before {
				@include fa-icon();
				content: $fa-var-home;
				font-size: 14px;
				color: $muni;
			}
		}
	}
	&__link{
		text-decoration: none;
	}

	&--secondary{
		margin: 0 0 $mMarginSmall;
		padding: 0 0 15px;
		background: transparent;
		p{
			margin-bottom: 0;
		}
	}

	@media(max-width: $tablet - 1){
		&__item{
			&--mobile{
				.icon-ellipsis-h{
					position: relative;
					top: 1px;
				}
			}
			&--mobile-hidden{
				display: none;
			}
		}
	}

	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		&:first-child{
			margin-top: -50px;
		}
		+ .box-hero,
		+ .box-bg,
		+ .u-mt-0{
			margin-top: -50px !important;
		}
		+ .box-hero--bordered{
			margin-top: -51px;
		}
		&__item{
			&--mobile{
				display: none;
			}
		}
	}
}
