.slick-slider {
	position: relative;
	user-select: none;
	touch-action: pan-y;
}
.slick-track,
.slick-list {
	transform: translate3d(0, 0, 0);
}
.slick-list {
	position: relative;
	overflow: hidden;
	&:focus {
		outline: 0;
	}
}
.slick-track {
	display: flex;
	width: 100% !important;
}
.slick-slide {
	display: flex;
	outline: 0;
	flex: 1 0 auto;
	& > div {
		width: 100%;
	}
	.grid__cell {
		max-width: 100%;
	}
}
.slick-initialized {
	display: block !important;
}

.slick-dots{
	@extend %reset-ul;
	position: static;
	text-align: left;
	padding-top: 12px;
	text-align: center;
	z-index: 200;
	margin-left: -5px;
	@include inline-list(auto, 0);
	li{
		@extend %reset-ul-li;
		margin: 0 0 5px 5px;
		&.slick-active{
			button{
				background: #fff;
			}
		}
	}
	button{
		padding: 0;
		width: 10px;
		height: 10px;
		border: 1px solid rgba(#fff, .5);
		border-radius: 50%;
		background: transparent;
		transition: opacity .2s, background .2s;
		@include hover() {
			&:hover {
				opacity: 1;
			}
		}
	}
}

.slick-slide.slick-current{
	.box-slider__item:before{
		opacity: 1;
	}
}

@media(min-width: $tablet) {
	.slick-dots{
		position: absolute;
		bottom: 10px;
		left: 30px;
		right: 30px;
		margin-left: -8px;
		li{
			margin: 0 0 5px 8px;
		}
	}
}
