$s: '.footer';

.footer{
	padding: 0 0 30px;
	background: #fff;
	border-bottom: 6px solid $primary;
	.share{
		text-align: center;
	}
	.menu-secondary{
		&:before{
			height: 1px;
			background: rgba($separator, .5);
		}
		&__wrap{
			height: 50px;
		}
		&__link{
			min-height: 50px;
		}
		.scroll-nav--background{
			background: #fff;
		}
		.scroll-nav--right{
			&:before{
				background-image: linear-gradient(to right, rgba(#fff, 0), rgba(#fff, 1));
			}
		}
		.scroll-nav--left{
			&:before{
			background-image: linear-gradient(to left, rgba(#fff, 0), rgba(#fff, 1));
			}
		}
	}
	&__claim{
		padding: 35px 0 45px;
		svg{
			fill: $muni;
		}
	}
	&__main-title{
		font-weight: bold;
		font-size: 24px;
		line-height: 32px;
		margin-bottom: 60px;
	}
	&__logo{
		margin-bottom: 0;
	}
	&__menu{
		margin: 0 0 50px;
		&__title{
			display: flex;
			align-items: flex-end;
			min-height: 52px;
			h2 {
				font-size: 16px;
				margin-bottom: 0;
				color: $muni;
			}
			&__link{
				-moz-transition: none;
			}
			&__link:visited{
				color: $linkVisited;
			}
			&__link {
				@include hover() {
					&:hover,
					&:visited:hover {
						color: $linkHover;
					}
				}
			}
		}
		&__list{
			@extend %reset-ul;
		}
		&__item{
			@extend %reset-ul-li;
		}
		&__link{
			-moz-transition: none;
		}
		&__link:visited{
			color: $linkVisited;
		}
		&__link{
			@include hover() {
				&:hover,
				&:visited:hover {
					color: $linkHover;
				}
			}
		}
	}
	&__links{
		border-bottom: 1px solid rgba(#ddd, .5);
		padding: 0 0 10px;
		margin: 0 0 25px;
		.grid__cell{
			font-size: 14px;
		}
	}
	&__copyrights{
		font-size: 14px;
		margin: 0 0 20px;
	}
	&__bottom{
		display: flex;
	}
	&__share{
		.icon{
			font-size: 22px;
		}
	}
	&__share-item{
		margin-right: 18px;
	}

	&--dark{
		background: $color;
		color: #fff;
		border: none;
		#{$s}__claim{
			svg{
				fill: #fff;
			}
		}
		#{$s}__menu,
		#{$s}__links,
		#{$s}__menu__title,
		#{$s}__share{
			border-color: #333;
		}
		#{$s}__menu{
			&__title{
				color: #fff;
			}
			&__link{
				@include hover() {
					&:hover {
						text-decoration: none;
					}
				}
			}
		}
		.crossroad-light{
			&__title{
				color: #fff;
			}
		}
		a:not(.share__item){
			color: #fff;
			@include hover() {
				&:hover {
					color: #fff;
				}
			}
		}
		.link-icon{
			.icon{
				color: #fff;
			}
			@include hover() {
				&:hover {
					text-decoration: none;
				}
			}
		}
		#{$s}__top-menu{
			background: #101010;
			.menu-secondary{
				&:before{
					background: transparent;
				}
				.scroll-nav--background{
					background: #101010;
				}
				.scroll-nav--right{
					&:before{
						background-image: linear-gradient(to right, rgba(#fff, 0), rgba(#101010, 1));
					}
				}
				.scroll-nav--left{
					&:before{
						background-image: linear-gradient(to left, rgba(#fff, 0), rgba(#101010, 1));
					}
				}
				&__link.is-active{
					&:after{
						background: #fff;
					}
				}
			}
		}
	}

	&--blue{
		background: $muni;
		#{$s}__menu,
		#{$s}__links,
		#{$s}__menu__title,
		#{$s}__share,
		#{$s}__claim{
			border-color: rgba(#fff, .25);
		}
		#{$s}__menu__link{
			@include hover() {
				&:hover {
					text-decoration: none;
				}
			}
		}
		#{$s}__top-menu{
			background: #0000C6;
			.menu-secondary{
				&:before{
					background: transparent;
				}
				.scroll-nav--background{
					background: #0000C6;
				}
				.scroll-nav--right{
					&:before{
						background-image: linear-gradient(to right, rgba(#fff, 0), rgba(#0000C6, 1));
					}
				}
				.scroll-nav--left{
					&:before{
						background-image: linear-gradient(to left, rgba(#fff, 0), rgba(#0000C6, 1));
					}
				}
				&__link.is-active{
					&:after{
						background: #fff;
					}
				}
			}
		}
		.link-icon{
			@include hover() {
				&:hover {
					text-decoration: none;
				}
			}
		}
	}

	@media(max-width: $tablet - 1){
		&__menu{
			border-bottom: 1px solid rgba($separator, .5);
			&__toggle{
				width: 100% !important;
				&.is-open{
					#{$s}__menu__box{
						display: block;
					}
					#{$s}__menu__title__toggle{
						&:before{
							content: $fa-var-minus;
							font-size: 20px;
						}
					}
				}
				#{$s}__menu__box{
					display: none;
				}
			}
			&__title{
				color: $muni;
				font-weight: bold;
				font-size: 16px;
				padding: 21px 0 21px 0;
				margin: 0;
				border-top: 1px solid #ddd;
				&.with-subitems{
					padding-right: 60px;
					position: relative;
				}
				&__toggle{
					position: absolute;
					right: 1px;
					top: 0;
					font-size: 0px;
					height: 60px;
					line-height: 60px;
					width: 60px;
					color: $muni;
					text-align: right;
					@include hover() {
						&:hover {
							color: $muni;
						}
					}
					&:before{
						display: inline-block;
						vertical-align: middle;
						font-size: 28px;
					}
				}
			}
			&__box{
				padding: 0 15px;
			}
			&__list{
				margin: 0 0 1.25em;
			}
			&__item{
				margin-top: .45em;
				&:first-child{
					margin-top: 0;
				}
			}
		}
		&__claim{
			br{
				display: none;
			}
		}
		&__bottom{
			flex-wrap: wrap;
		}
		&__share,
		&__copyrights{
			flex: 1 0 100%;
		}
		&__copyrights{
			order: 2;
		}
		&__share{
			padding-bottom: 25px;
			border-bottom: 1px solid rgba(#ddd, .5);
		}
	}

	@media(min-width: $tablet){
		padding: 0 0 90px;
		&__menu{
			padding: 40px 0 60px;
			border-bottom: 1px solid rgba($separator, .5);
			&__title{
				margin: 0 0 25px;
				h2 {
					font-size: 18px;
					text-transform: uppercase;
					font-family: $fontMuni;
					color: $dark;
					font-weight: bold;
				}
				&__link{
					text-decoration: none;
				}
				&__toggle{
					display: none;
				}
			}
			&__list{
				font-size: 14px;
			}
			&__item{
				margin: 0 0 10px;
			}
		}
		&__copyrights{
			text-align: left;
		}
		&__claim{
			border-bottom: 1px solid rgba($separator, .5);
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 50px 0 60px;
			p{
				margin-bottom: 0;
			}
		}
		&--main-title{
			font-size: 30px;
			line-height: 34px;
		}
		&__links{
			padding: 0 0 23px;
			margin: 0 0 40px;
		}
		&__bottom{
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		&__share-item{
			&:last-child{
				margin-right: 0;
			}
		}
		.menu-secondary{
			&__wrap{
				height: 77px;
			}
			&__item{
				min-height: 77px;
			}
		}
	}

	@media(min-width: $desktopWide) {
		.menu-secondary{
			&__list{
				display: flex;
				align-items: center;
			}
			&__item{
				flex: 0 0 auto;
			}
		}
	}
}
