$s: '.header';

.header{
	border-top: 6px solid $primary;
	position: relative;
	margin-bottom: 50px;
	background: #fff;
	&__wrap{
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding: 11px 0 10px;
	}
	&__logo{
		margin: 0 15px 0 0;
		// clear larger element height caused by large h1
		font-size: 0;
		a{
			display: inline-block;
			vertical-align: top;
			z-index: 1;
			position: relative;
		}
		svg{
			vertical-align: top;
		}
		&--no-margin {
			margin-top: -18px !important;
			overflow: hidden;
			@media (max-width: $tablet - 1) {
				margin-top: -11px !important;
				&--crop svg {
					margin-top: -7px;
				}
			}
		}

		// leave space for hamburger & co. and scrollbar
		@media (max-width: $tablet - 1){
			max-width: calc(100vw - 30px - 95px - 20px);
		}
	}
	&__side{
		display: flex;
		justify-content: flex-end;
		align-items: center;
		flex-grow: 1;
	}
	// SEARCH
	&__search{
		fieldset {
			display: none;
		}
		p{
			margin: 0;
		}
		.inp-text{
			&::placeholder{
				color: #505050;
			}
		}
	}
	// HEADER MODIFICATIONS
	// HERO HEADER
	&--hero {
		border-bottom: 0;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		background: transparent;
		z-index: 10;
		&:not(.no-gradient) {
			background-image: linear-gradient(to bottom, #000 1%, rgba(#000, .3) 80%, rgba(#000, 0));
		}
		.header-min ~ &{
			top: 40px;
		}
	}
	&--hero:not(.header--hero--light) {
		#{$s}__logo{
			path,
			polygon {
				fill: #fff !important;
			}
		}
		.menu-header-secondary__link{
			color: #fff;
			&.is-active {
				color: #fff;
			}
			@include hover() {
				&:hover {
					color: rgba(#fff, .85);
				}
			}
			.icon {
				color: #fff;
			}
		}
		.menu-lang--select.is-hover{
			.menu-lang__selected__link{
				color: $muni;
				&:before{
					color: $muni;
				}
				@include hover() {
					&:hover {
						color: $color;
						&:before{
							color: $color;
						}
					}
				}
			}
		}
		.menu-lang__selected__link{
			color: #fff;
			-moz-transition: none;
			&:before{
				color: #fff;
			}
			@include hover() {
				&:hover,
				&:visited:hover {
					color: rgba(#fff, .8);
					&:before {
						color: rgba(#fff, .8);
					}
				}
			}
		}
		.menu-primary {
			&__title,
			&__link {
				color: #fff;
				&.is-active {
					color: #fff;
				}
				.icon {
					color: #fff;
				}
			}
			&__title{
				@include hover() {
					&:hover {
						&:before,
						&:after,
						span{
							background: rgba(#fff,.8);
						}
					}
				}
				&:before,
				&:after,
				span{
					background: #fff;
				}
			}
			&__link{
				@include hover() {
					&:hover {
						color: rgba(#fff, .85);
					}
				}
			}
			&__item{
				&.is-open{
					.menu-primary__link{
						color: $dark;
					}
				}
			}
			&__main-toggle {
				.menu-primary__link, .menu-header-secondary__link {
					color: $muni;
				}
			}
		}
		
		.menu-secondary{
			&__link{
				color: #fff;
				@include hover() {
					&:hover {
						color: rgba(#fff, .85);
					}
				}
			}
		}
		.menu-mobile{
			&__link{
				color: #fff;
				@include hover() {
					&:hover {
						color: rgba(#fff, .8);
					}
				}
			}
			&__burger{
				&:before,
				&:after,
				span{
					background: #fff;
				}
			}
		}
	}
	// mobile
	@media(max-width: $tablet - 1){
		margin: 0;
		&__logo{
			#logo {
				display: none;
			}
			svg{
				max-width: 100%;
				max-height: 53px;
				width: auto;
				height: auto;
			}
		}
		// SEARCH
		&__search{
			position: absolute;
			left: 0;
			right: 0;
			top: 100%;
			visibility: hidden;
			opacity: 0;
			//transition: opacity .2s;
			display: block !important;
			z-index: 1000;
			fieldset{
				display: block;
			}
			&__label{
				display: none;
				position: absolute;
				right: 15px;
				top: 18px;
				color: $muni;
			}
			.inp-fix{
				border: 15px solid #fff;
				margin-top: 1px;
				box-shadow: 0 1px 0 $border;
			}
			&.is-open{
				opacity: 1;
				visibility: visible;
				#{$s}__label{
					&:before{
						content: $fa-var-times;
					}
				}
				// .inp-fix{

				// }
			}
		}
	}
	// tablet only
	@media(min-width: $tablet) and (max-width: $desktop - 1){
		.size--m--1-3{
			width: 33.333%;
			margin-bottom: 30px;
		}
	}
	// tablet
	@media(min-width: $tablet){
		&__logo{
			padding: 0 15px 15px 0;
			#symbol {
				display: none;
			}
		}
		// FIXED
		transition: transform .3s;
		&.headroom--pinned{
			transform: translateY(0%);
		}
		&.headroom--unpinned{
			transform: translateY(-100%);
		}
		&__wrap{
			padding: 18px 0 8px;
		// 	align-items: flex-start;
		}
		// SEARCH
		&__search{
			max-width: 277px;
			width: 100%;
			fieldset{
				display: block;
			}
			p{
				margin: 0;
			}
			.inp-fix{
				margin-top: 0;
			}
			.inp-text{
				font-size: 18px;
			}
			&__label{
				display: block;
				position: absolute;
				left: -5000px;
				top: -5000px;
			}
		}
		&--hero:not(.header--hero--light) {
			#{$s}__search{
				.inp-text{
					color: #fff;
					background: transparent;
					&::placeholder{
						color: #fff;
					}
					&:focus{
						border-color: #d5d5d5;
						&::placeholder{
							color: rgba(#fff, .85);
						}
					}
				}
				.icon-search{
					color: #fff;
				}
				&__label {
					color: #fff;
				}
			}
		}
		//HEADER MODIFICATIONS
		// nav under header
		&--nav-under{
			padding-bottom: 10px;
			.menu-primary{
				&__wrap{
					padding-top: 10px;
					margin: 7px 0 0 0;
					position: static;
					min-height: 47px;
					@include clearfix();
				}
				&__item{
					&:first-child {
						margin-left: 0;
					}
				}
				&__title{
					top: 30px;
				}
			}
		}
	}
	// desktop
	@media(min-width: $desktop){
		// HEADER MODIFICATIONS
		// wrap + fixed
		&-fixed-wrap{
			height: 179px;
			&.is-fixed{
				#{$s}{
					//min-height: 80px;
					background: #fff;
					position: fixed;
					&.header--full{
						> .row-main{
							display: block;
						}
					}
					> .row-main{
						display: none;
					}
					.menu-primary__title{
						top: 37px;
					}
				}
			}
			#{$s}{
				position: absolute;
				left: 0;
				right: 0;
				top: 0;
				z-index: 500;
			}
		}
	}
}
