$s: '.box-vcard';

.box-vcard {
	margin: 0 0 $mMarginSmall;
	.table-vcard {
		clear: none;
		margin-bottom: 23px;
	}
	&__inner {
		display: block;
		text-decoration: none;
		padding: 15px 0;
		&:not(.no-border) {
			border: 2px solid $color;
			border-width: 2px 0;
		}
		@include clearfix();
		transition: color .2s;
		@include hover() {
			&[href]:hover {
				color: $color;
				#{$s}__name{
					color: $color;
				}
			}
		}
		&[href]{
			color: $color;
			#{$s}__name{
				color: $muni;
			}
			&:visited{
				#{$s}__name {
					color: $linkVisited;
				}
			}
			@include hover() {
				&:visited:hover {
					#{$s}__name{
						color: $linkHover;
					}
				}
			}
		}
	}
	&__img{
		text-align: center;
		margin: 0 0 10px;

		#{$s}--round-photo & img {
			border-radius: 50%;
		}
	}
	&__name {
		font-size: 21px;
		margin-bottom: 0;
		text-align: center;

		#{$s}--noimg & {
			text-align: left;
		}
	}
	&__position {
		font-size: 18px;
		margin-bottom: 0;
		text-align: center;

		#{$s}--noimg & {
			text-align: left;
		}
	}
	&__content {
		font-size: 16px;
		display: table-cell;
		&:after{
			clear: both;
			display: block;
			visibility: hidden;
			overflow: hidden;
			height: 0px !important;
			line-height: 0px;
			font-size: xx-large;
			content: " x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x x ";
		}
		hr {
			margin: 25px 0;
			border-top: 2px solid $color;
			width: 35px;
		}
		ul {
			padding: 0;
			margin: 0;
		}
		li {
			margin: 0;

			&:before {
				top: .7em;
			}
		}
		p{
			font-size: 16px;
			margin-bottom: 23px;
		}
		> *:last-child {
			margin-bottom: 0;
		}
		#{$s}--noimg &{
			padding-left: 0 !important;
		}
	}
	&__foot {
		border-bottom: 2px solid $color;
		margin: 0;
		padding: 15px 0;
		> *:last-child {
			margin-bottom: 0;
		}
	}

	&--simple,
	&--compact {
		#{$s}__name {
			font-size: 18px;
			text-align: left;
			margin-bottom: 5px;
		}
		#{$s}__position {
			text-align: left;
		}
		#{$s}__inner {
			display: table;
			width: 100%;
			padding: 0;
			> * {
				display: table-cell;
				vertical-align: middle;
			}
		}
		#{$s}__img {
			width: 105px;
			float: none;
			margin: 0;
			padding: 0;
			vertical-align: top;
			img{
				max-width: none;
			}
		}
		#{$s}__content {
			padding: 15px;
		}
		.table-vcard {
			font-size: 14px;
		}
		#{$s}__position {
			margin-bottom: 10px;
		}
		#{$s}__foot {
			margin-bottom: -15px !important;
			padding-left: 0;
		}
	}
	&--noborder{
		#{$s}__inner{
			border-color: transparent;
		}
		#{$s}__foot{
			border-color: transparent;
		}
	}

	@media(min-width: $tablet) {
		margin: 0 0 $mMargin;
		&__inner{
			padding: 30px 30px 30px 250px;
		}
		&__img{
			float: left;
			margin: 0 0 0 -250px;
			width: 230px;
			padding-right: 20px;
		}
		&__name{
			font-size: 24px;
			text-align: left;
		}
		&__position{
			text-align: left;
		}
		&__foot{
			padding: 20px 30px 30px 0;
			p{
				font-size: 16px;
			}
		}

		&--noimg {
			#{$s}__inner {
				padding-left: 0;
			}
		}

		&--small-img {
			#{$s}__inner{
				padding-left: 200px;
			}
			#{$s}__img{
				width: 169px;
				margin: 0 0 0 -200px;
				img{
					max-width: 139px;
					width: 100%;
					height: auto;
				}
			}
		}

	}

	@media(min-width: $desktop){

		&--noimg {
			#{$s}__inner {
				padding-left: 0;
			}
		}

		&--simple,
		&--compact {
			#{$s}__name {
				font-size: 24px;
			}
			.table-vcard {
				font-size: 14px;
			}
			#{$s}__name {
				font-size: 18px;
			}
			#{$s}__position {
				font-size: 14px;
			}
		}
	}

	@media(min-width: $desktopWide){
		&--simple,
		&--compact {
			#{$s}__content {
				padding: 30px 0;
			}
			#{$s}__content {
				padding: 18px 25px;
			}
		}
	}
}
