$s: '.box-hero-header';

.box-hero-header {
	position: relative;
	margin-bottom: $mMarginSmall;
	min-height: 350px;
	&--bg {
		#{$s} {
			&__content{
				background: rgba($color, 0.2);
			}
		}
	}
	&--full{
		min-height: 100vh;
		#{$s} {
			&__content{
				padding-top: 50px;
				&--bottom{
					.row-main{
						&:before{
							display: inline-block;
							vertical-align: bottom;
							height: calc(100vh - 100px);
							width: 1px;
							margin-left: -.25em;
						}
					}
					#{$s}{
						&__content-inner{
							display: inline-block;
							vertical-align: bottom;
							width: 470px;
							max-width: 99%;
						}
					}
				}
			}
		}
		.header--hero ~ .main > &:first-child{
			#{$s} {
				&__content{
					padding-top: 170px;
					&--bottom{
						.row-main{
							&:before{
								height: calc(100vh - 180px);
							}
						}
					}
					@media (min-width: $tablet) {
						padding-top: 250px;
						&--bottom{
							.row-main{
								&:before{
									height: calc(100vh - 300px);
								}
							}
						}
					}
				}
			}
		}
	}
	&__img,
	&__video {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
	}
	&__img {
		background-size: cover;
		background-position: 50% 50%;
		background-repeat: no-repeat;
	}
	&__video {
		overflow: hidden;
		video {
			position: absolute;
			top: 50%;
			left: 50%;
			width: auto;
			height: auto;
			min-width: 100%;
			min-height: 100%;
			transform: translate(-50%, -50%);
		}
	}
	&__content {
		position: relative;
		padding-top: 170px;
		padding-bottom: 50px;
		&--bg {
			#{$s} {
				&__content-inner {
					background: $color;
					padding: 15px 20px 5px;
					margin-bottom: 0;
					> * {
						color: #fff;
						border: none !important;
					}
					a {
						color: #fff;
						@include hover() {
							&:hover {
								text-decoration: none;
							}
						}
					}
					h1{
						padding: 0;
					}
					.btn-wrap{
						margin-top: 30px;
					}
				}
			}
			&--white {
				#{$s} {
					&__content-inner {
						background: #fff;
						> * {
							color: $dark;
						}
						a {
							color: $link;
							@include hover() {
								&:hover {
									color: $dark;
								}
							}
						}
					}
				}
			}
		}
		&--right{
			text-align: right;
			#{$s}{
				&__content-inner{
					margin-left: auto;
				}
			}
		}
		&--full-width{
			#{$s}{
				&__content-inner{
					max-width: none !important;
				}
			}
		}
	}
	&__content-inner {
		max-width: 515px;
		text-align: left;
		h1{
			font-family: $font;
			border: 2px solid currentColor;
			border-width: 2px 0;
			padding: 10px 0;
		}
		p{
			line-height: 22px;
			margin-bottom: 10px;
		}
	}
	&__separator{
		border-top: 2px solid currentColor;
	}

	@media (max-width: $tablet - 1) {
		&__content {
			padding-bottom: 20px;
		}
	}

	@media (min-width: $tablet) {
		margin-bottom: $mMargin;
		&__content {
			padding-top: 350px;
			&--bg {
				padding-top: 250px;
				#{$s} {
					&__content-inner {
						max-width: 470px;
						padding: 35px 45px 10px 35px;
					}
				}
			}
		}
		&__content-inner{
			h1{
				font-size: 44px;
			}
		}
	}
}
