$s: '.box-slider';

.box-slider-pager {
	margin-bottom: $mMarginSmall;
	.box-slider {
		margin-bottom: 0;
		z-index: 1;
		&__wrap {
			margin-top: -1px !important;
			margin-bottom: 0;
		}
		&--carousel {
			#{$s}__list {
				margin-left: 0;
			}
			#{$s}__item {
				flex: 0 0 33.333%;
				width: 33.333%;
				position: relative !important;
				padding-left: 0;
				cursor: pointer;
				box-sizing: border-box;
				&:before,
				&:after {
					content: '';
					position: absolute;
					left: 0;
					width: 100%;
					background: $muni;
					opacity: 0;
					transition: opacity .2s ease-out;
				}
				&:before {
					bottom: 0;
					height: 5px;
					z-index: 200;
				}
				&:after {
					bottom: 0;
					top: 0;
				}
				@include hover() {
					&:hover {
						&:after {
							opacity: .7;
						}
					}
				}
			}
			#{$s}__prev,
			#{$s}__next {
				top: 45%;
				width: 30px;
				height: 50%;
				font-size: 0;
				&:before {
					content: '';
					height: 100%;
					display: inline-block;
					vertical-align: middle;

				}
				.icon {
					display: inline-block;
					vertical-align: middle;
					font-size: 28px;
					line-height: 30px;
				}
			}
			#{$s}__prev {
				right: auto;
				left: -30px;
			}
			#{$s}__next {
				left: auto;
				right: -30px;
			}
		}
	}
	@media (min-width: $tablet) {
		margin: 0 0 $mMargin;
	}
	@media(max-width: 1239px) {
		.box-slider--carousel {
			margin-left: 20px;
			margin-right: 20px;
			padding-bottom: 0;
		}
	}
}
