.box-quote {
	text-align: center;
	padding: ($mMarginSmall + 15) 0 $mMarginSmall;
	margin-bottom: $mMarginSmall;
	position: relative;
	border: 2px solid $color;
	border-width: 2px 0;
	.row-main{
		blockquote{
			padding: 0;
		}
	}
	blockquote {
		max-width: 980px;
		margin: 0 auto;
		position: relative;
		padding: 0 20px;
	}
	p {
		font-weight: bold;
		font-size: 18px;
		margin-bottom: 0;
	}
	cite {
		font-size: 16px;
		font-style: normal;
		display: inline-block;
		margin-top: 15px;
		padding-top: 15px;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 50%;
			margin-left: -35px;
			width: 70px;
			height: 2px;
			background: rgba($color, .5);
		}
	}

	&--bg {
		border: none;
		background: $muni;
		p,
		cite {
			color: #fff;
		}
		cite {
			&:before {
				background: rgba(#fff, .5);
			}
		}
	}

	@media (min-width: $tablet) {
		margin: 0 0 $mMargin;
		padding: ($mMargin + 40) 0 $mMargin;
		p {
			font-size: 24px;
		}
		cite {
			margin-top: 35px;
			padding-top: 25px;
		}
	}

	@media (min-width: $desktop) {
		p {
			font-size: 30px;
			line-height: 34px;
		}
	}
}
