$s: '.box-product';

.box-product{
	display: block;
	&__inner {
		text-decoration: none;
		@include hover() {
			&[href]:hover{
				#{$s}__title{
					color: $linkHover;
					text-decoration: underline;
				}
			}
		}
		&[href]{
			&:visited{
				#{$s}__title {
					color: $linkVisited;
				}
			}
			@include hover() {
				&:visited:hover {
					#{$s}__title{
						color: $linkHover;
					}
				}
			}
		}
	}
	&__img{
		margin: 0;
		position: relative;
		text-align: center;
		img{
			width: 100%;
			height: auto;
		}
	}
	&__labels{
		position: absolute;
		left: 10px;
		top: 15px;
		margin-bottom: -10px;
		.tag{
			float: left;
			clear: both;
			margin-bottom: 10px;
		}
	}
	&__content{
		padding: 10px 0 15px;
	}
	&__title{
		color: $muni;
		font-size: 16px;
		line-height: 1.2em;
		margin: .5em 0 15px;
		padding-bottom: 18px;
		position: relative;
		transition: color .2s;
		&:before{
			content: '';
			position: absolute;
			left: 0;
			bottom: 0;
			width: 35px;
			height: 2px;
			background: $color;
		}
	}
	&__author{
		display: block;
		color: $color;
		font-size: 16px;
		font-weight: 400;
		margin-top: 10px !important;
	}
	&__price{
		color: $color;
		font-size: 24px;
		font-weight: bold;
		font-family: $fontMuni;
		margin-bottom: 0;
	}
	&__stock{
		display: block;
		font-size: 14px;
		line-height: 1.2;
	}

	&--secondary{
		overflow: hidden;
		#{$s}__img{
			float: left;
			max-width: 145px;
		}
		#{$s}__content{
			padding: 0 0 0 160px;
		}
		#{$s}__labels{
			left: 5px;
			top: 10px;
		}
	}

	@media (min-width: $tablet){
		&__content{
			padding: 15px 0 20px;
		}
		&__title{
			font-size: 18px;
		}
		&--secondary{
			#{$s}__img{
				max-width: 155px;
			}
			#{$s}__content{
				padding: 0 0 0 175px;
			}
		}
	}
}
