$s: '.box-workplaces';

.box-workplaces {
	margin-bottom: $mMarginSmall;
	&__title {
		font-family: $fontMuni;
		margin-bottom: 25px;
		font-size: 24px;
	}
	&__wrap {
		border: 2px solid $color;
		border-width: 2px 0;
		padding: 60px 20px 0;
		+ #{$s}__wrap {
			margin-top: -2px;
		}
	}

	&--background{
		background: #fff;
		border: 1px solid $border;
		padding: 0 20px;
		#{$s}__wrap{
			border-width: 0 0 2px;
			padding: 60px 0 0;
			&:last-child{
				border: none;
			}
		}
	}

	@media (min-width: $tablet) {
		margin-bottom: $mMargin;
		&__wrap {
			padding: 65px 40px 10px;
		}
		&--background{
			#{$s}__wrap{
				padding: 65px 0 0;
			}
		}
	}

	@media (min-width: $desktop) {
		&__title {
			float: left;
			margin-left: -345px;
			max-width: 250px;
		}
		&__wrap {
			padding: 65px 20px 60px 415px;
			@include clearfix();
		}
		&--background{
			background: #fff;
			border: 1px solid $border;
			padding: 0 40px;
			#{$s}__wrap{
				padding: 65px 20px 60px 375px;
			}
		}
	}
}
