.box-bg{
	margin: 0 0 $mMarginSmall;
	padding: 15px;
	background: #fff;
	> .row-main > :last-child,
	> :last-child{
		margin-bottom: 0;
	}
	.menu-inside {
		&:before {
			display: none;
		}
	}
	&--color-light{
		&, h1, h2, h3, h4, h5, h6, p, ul, ol, a, cite, cite strong {
			color: $whiteInvertable;
		}
		a {
			@include hover() {
				&:hover {
					color: $whiteInvertable;
					text-decoration: none;
				}
			}
		}
	}
	&--primary {
		background: $primary;
	}
	&--muni {
		background: $muni;
	}
	&--law {
		background: $law;
	}
	&--med {
		background: $med;
	}
	&--sci {
		background: $sci;
	}
	&--fi {
		background: $fi;
		&, h1, h2, h3, h4, h5, h6, p, ul, ol, a, cite, cite strong {
			color: $color;
		}
	}
	&--fss {
		background: $fss;
	}
	&--fsps {
		background: $fsps;
	}
	&--phil {
		background: $phil;
		&, h1, h2, h3, h4, h5, h6, p, ul, ol, a, cite, cite strong {
			color: $color;
		}
	}
	&--ped {
		background: $ped;
		&, h1, h2, h3, h4, h5, h6, p, ul, ol, a, cite, cite strong, p {
			color: $color;
		}
	}
	&--econ {
		background: $econ;
	}
	&--pharm {
		background: $pharm;
	}
	&--dark {
		background: $dark;
	}
	&--color {
		background: $color;
	}
	&--grey-dark {
		background: $greyDark;
	}
	&--grey-light {
		background: $greyLight;
	}
	&--white-border {
        background: #fff;
        border: 1px solid $borderPrimary;
	}
	&--muni-border {
		border: 2px solid black;
		border-width: 2px 0;
		background: none;
	}
    @if (variable-exists(customColors)) {
        @each $customColor in $customColors {
            &--#{nth($customColor, 1)} {
                background: #{nth($customColor, 2)};
                color: #{nth($customColor, 3)};
            }
        }
    }
	&--full {
		padding-left: 0;
		padding-right: 0;
	}

	&--padding{
		padding-bottom: 0 !important;
	}

	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		padding: 50px 30px 40px;
		&--small {
			padding: 15px;
		}
		&--full {
			padding-left: 0;
			padding-right: 0;
		}
	}

	@media(min-width: $desktop){
		&--small {
			padding: 15px;
		}
		&--medium {
			padding: 30px;
		}
		&--full {
			padding-left: 0;
			padding-right: 0;
		}
	}
}
