$s: '.scroll-nav';

.scroll-nav {
	position: absolute;
	top: 0;
	bottom: 2px;
	width: 30px;
	background: #fff;
	z-index: 101;
	&:before {
		content: '';
		position: absolute;
		top: 0;
		bottom: 0;
		width: 30px;
	}
	.icon {
		position: absolute;
		top: 50%;
		left: 50%;
		font-size: 14px;
		margin: -6px 0 0 -5px;
	}

	&--right {
		right: 0;
		&:before {
			right: 100%;
			background-image: linear-gradient(to right, rgba(#fff, 0), rgba(#fff, 1));
		}
	}

	&--left {
		left: 0;
		&:before {
			left: 100%;
			background-image: linear-gradient(to left, rgba(#fff, 0), rgba(#fff, 1));
		}
	}

	.box-bg & {
		background: $greyLight;
		&#{$s}--right {
			&:before {
				background-image: linear-gradient(to right, rgba($greyLight, 0), rgba($greyLight, 1));
			}
		}
		&#{$s}--left {
			&:before {
				background-image: linear-gradient(to left, rgba($greyLight, 0), rgba($greyLight, 1));
			}
		}
	}
}
