 $s: '.menu-secondary';

.menu-secondary{
	position: relative;
	header:not(.header--hero--noline) &:before{
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 2px;
		background: rgba($greyDark, .5);
		z-index: 5;
	}
	&__wrap {
		white-space: nowrap;
		overflow: hidden;
		height: 50px;
	}
	&__list{
		@extend %reset-ul;
		font-size: 0px;
		display: inline-block;
		vertical-align: top;
		max-width: 100%;
		overflow-x: auto;
		overflow-y: visible;
		-webkit-overflow-scrolling: touch;
	}
	&__item{
		@extend %reset-ul-li;
		display: inline-block;
		vertical-align: top;
		margin: 0 10px;
		&:first-child{
			margin-left: 0;
		}
		&:last-child{
			margin-right: 0;
		}
	}
	&__list {
		#{$s}__item:first-child a:not(.with-icon){
			padding-left: 0;
		}
		#{$s}__item:last-child a{
			padding-right: 0;
		}
	}
	&__link{
		color: $muni;
		font-size: 16px;
		text-decoration: none;
		display: block;
		padding: 13px 0 14px 0;
		position: relative;
		transition: background .2s, color .2s;
		z-index: 100;
		white-space: nowrap;
		&:after{
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			height: 2px;
			background: transparent;
			transition: background .2s;
		}
		.icon-angle-down{
			font-size: 16px;
			position: relative;
			top: 1px;
		}
		&.with-icon{
			padding-left: 20px;
			.icon{
				position: absolute;
				left: 0;
				top: 50%;
				font-size: 14px;
				margin-top: -7px;
			}
		}
		&.with-dropdown{
			padding-right: 20px !important;
			.icon-angle-down{
				position: absolute;
				left: auto;
				right: 0;
				top: 50%;
				font-size: 14px;
				margin-top: -7px;
			}
		}
		&.is-active{
			color: $color;
			font-weight: bold;
			&:after{
				background: currentColor;
			}
		}
		@include hover() {
			&:hover {
				color: $color;
			}
		}
		&.is-open,
		&.is-hover{
			color: $color;
			.icon-angle-down{
				transform: rotate(180deg);
			}
		}
	}
	&__submenu{
		position: absolute;
		left: 15px;
		top: 100%;
		margin: 0;
		min-width: 250px;
		z-index: 102;
		background: #fff;
		border: 1px solid $border;
		font-size: 14px;
		visibility: hidden;
		opacity: 0;
		transition: opacity .2s;
		&--wide {
			min-width: 300px;
		}
		&__list {
			@extend %reset-ul;
			&:first-child {
				margin-top: 0;
			}
		}
		&__item{
			@extend %reset-ul-li;
		}
		&__item:last-child{
			#{$s}__submenu__link{
				&:before{
					display: none;
				}
			}
		}
		&__item:first-child{
			#{$s}__submenu__link{
				&:after{
					display: none;
				}
			}
		}
		&__link{
			font-size: 16px;
			display: block;
			padding: 14px 22px !important;
			color: $muni;
			text-decoration: none;
			position: relative;
			transition: background .2s;
			-moz-transition: none;
			&:before,
			&:after{
				content: '';
				position: absolute;
				left: 22px;
				right: 22px;
				height: 1px;
				transition: background .2s, opacity .2s;
			}
			&:before{
				bottom: 0;
				background: $border;
			}
			&:after{
				top: -1px;
				background: #fff;
				opacity: 0;
			}
			@include hover() {
				&:hover {
					background: $muni;
					color: #fff;
					&:before{
						background: $muni;
					}
					&:after{
						opacity: 1;
					}
				}
			}
			@include hover() {
				&:visited:hover {
					color: #fff;
				}
			}
			&.with-icon{
				padding-left: 46px !important;
				.icon{
					position: absolute;
					left: 22px;
					top: 50%;
					font-size: 14px;
					margin-top: -8px;
				}
			}
		}
		&.is-open {
			opacity: 1;
			visibility: visible;
		}
	}

	&--right{
		text-align: right;
	}

	&--center{
		text-align: center;
	}

	&--white{
		.scroll-nav--background{
			background: #fff;
		}
		.scroll-nav--right{
			&:before{
				background-image: linear-gradient(to right, rgba(#fff, 0), rgba(#fff, 1));
			}
		}
		.scroll-nav--left{
			&:before{
				background-image: linear-gradient(to left, rgba(#fff, 0), rgba(#fff, 1));
			}
		}
	}

	@media (max-width: $tablet - 1) {
		.row-main {
			padding: 0 10px;
		}
		&__submenu {
			width: 100%;
			left: 0;
		}
	}

	@media (min-width: $tablet) {
		.header--nav-under & {
			margin: 0 0 -12px;
		}
		.main &{
			margin-bottom: 0;
		}
		&__item{
			margin: 0 20px;
		}
		&__link {
			font-size: 16px;
			&.with-icon{
				padding-left: 25px;
			}
		}
		&__submenu{
			margin: 0 0 0 -20px;
		}

		&--l{
			#{$s}__item{
				margin: 0 21px;
				&:first-child{
					margin-left: 0;
				}
				&:last-child{
					margin-right: 0;
				}
			}
			#{$s}__link{
				padding: 27px 0;
			}
		}
	}
}
