$s: '.box-hero';

.box-hero {
	position: relative;
	display: block;
	margin: 0 0 $mMarginSmall;
	border: 1px solid transparent;
	text-decoration: none;
	color: $color;
	background: #fff;

	@include hover() {
		&[href]:hover {
			text-decoration: none;
			color: $color;
			h2,
			#{$s}__title{
				text-decoration: underline;
			}
		}
	}
	.row-main {
		position: static;
	}

	// title
	&__title{
		&--light{
			font-weight: normal;
			strong{
				border-bottom: 4px solid $primary;
			}
		}
	}

	// image
	&__img {
		position: relative;
		top: -1px;
		margin-left: -16px;
		margin-right: -16px;
		overflow: hidden;
		&:before {
			content: '';
			display: block;
			padding-top: 300px;
		}
		img {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			max-width: none;
			min-height: 100%;
			min-width: 100%;
			width: auto;
			height: auto;
		}
	}

	// content
	&__content {
		display: table;
		table-layout: fixed;
		width: 100%;
		position: relative;
		z-index: 1;
		ul:not(.crossroad-links__list) {
			li {
				&:before {
					background: currentColor;
				}
			}
		}
		ol {
			li {
				&:before {
					color: currentColor;
				}
			}
		}
	}
	&__content-inner {
		display: table-cell;
		vertical-align: middle;
		padding: 20px 0px 25px;
		position: relative;
		> * {
			max-width: $heroBoxMaxWidth;
			&:last-child {
				margin-bottom: 0;
			}
		}
		> .center{
			margin-left: auto;
			margin-right: auto;
		}
	}

	// prevent image stretch if herobox in slider
	.box-slider & .image img {
		min-width: initial;
	}

	// in row-main
	.row-main & &__content-inner {
		padding-left: 20px;
		padding-right: 20px;
		&:before,
		&:after{
			left: 20px;
			right: 20px;
		}
	}

	.row-main & &__img {
		margin-left: -1px;
		margin-right: -1px;
	}

	// VARIANTS
	&--space {
		margin: $mMargin 0;
	}
	&--bordered {
		border-color: rgba($border, .5);
	}
	&--transparent{
		background-color: transparent;
	}
	&--background {
		background: $muni;
		color: $whiteInvertable;
		h1, h2, h3, h4, h5, h6, p, ul, ol, a, cite, cite strong {
			color: $whiteInvertable;
		}
		.crossroad-links__item:before {
			color: $whiteInvertable;
		}
		#{$s}__content {
			p:first-child {
				border-color: #fff;
			}
			&-inner {
				a {
					transition: opacity .2s;
					@include hover() {
						&:hover {
							color: #fff;
							opacity: 0.8;
						}
					}
				}
			}
		}
		@include hover() {
			&:hover {
				color: #fff !important;
				text-decoration: none;
			}
		}
		&-muni {
			background-color: $muni;
		}
		&-law {
			background-color: $law;
		}
		&-med {
			background-color: $med;
		}
		&-sci {
			background-color: $sci;
		}
		&-fi {
			background-color: $fi;
			h1, h2, h3, h4, h5, h6, p, ul, ol, a, cite, cite strong {
				color: $color;
			}
			#{$s}__content {
				&-inner {
					a {
						transition: opacity .2s;
						@include hover() {
							&:hover {
								color: $color;
								opacity: 0.8;
							}
						}
					}
				}
			}
		}
		&-fss {
			background-color: $fss;
		}
		&-fsps {
			background-color: $fsps;
		}
		&-phil {
			background-color: $phil;
			h1, h2, h3, h4, h5, h6, p, ul, ol, a, cite, cite strong {
				color: $color;
			}
			#{$s}__content {
				&-inner {
					a {
						transition: opacity .2s;
						@include hover() {
							&:hover {
								color: $color;
								opacity: 0.8;
							}
						}
					}
				}
			}
		}
		&-ped {
			background-color: $ped;
			h1, h2, h3, h4, h5, h6, p, ul, ol, a, cite, cite strong {
				color: $color;
			}
			#{$s}__content {
				&-inner {
					a {
						transition: opacity .2s;
						@include hover() {
							&:hover {
								color: $color;
								opacity: 0.8;
							}
						}
					}
				}
			}
		}
		&-econ {
			background-color: $econ;
		}
		&-pharm {
			background-color: $pharm;
		}
		&-color {
			background: $color;
		}
		&-dark {
			background: $dark;
		}
		&-grey-dark {
			background: $greyDark;
		}
		&-grey-light {
			background: $greyLight;
		}
		@if (variable-exists(customColors)) {
			@each $customColor in $customColors {
				&-#{nth($customColor, 1)} {
					background: #{nth($customColor, 2)};
					color: #{nth($customColor, 3)};
				}
			}
		}
	}
	&--background-img{
		border: none;
		background: $primary 50% 50% no-repeat;
		background-size: cover;

		&:not(.no-white) {
			color: $whiteInvertable;

			h1, h2, h3, h4, h5, h6, p, ul, ol, a, cite, cite strong {
				color: $whiteInvertable;
			}

			.box-testimonial{
				border-color: $whiteInvertable;
			}
		}

		@include hover() {
			&:not(.no-white) a:hover {
				color: $whiteInvertable;
			}
		}
		@include hover() {
			a:hover {
				text-decoration: none;
			}
		}

		&:not(.no-overlay):before {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background: rgba(#000, .2);
		}
		.row-main {
			z-index: 1;
		}
	}
	&--background-img-gradient{
		border: none;
		background: $primary 50% 50% no-repeat;
		background-size: cover;
		&:before{
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background: linear-gradient(to right, #fff 0%, rgba(#fff, 0) 50%, rgba(#fff, 0) 100%);
		}
		.row-main {
			z-index: 1;
		}
		#{$s}__content {
			position: relative;
			z-index: 1;
		}
	}
	&--border{
		#{$s}__content-inner{
			&:before,
			&:after{
				content: '';
				position: absolute;
				left: 0;
				right: 0;
				height: 2px;
				background: currentColor;
			}
			&:before{
				top: 10px;
			}
			&:after{
				bottom: 10px;
			}
		}
	}

	// MEDIA
	@media (min-width: $mobileL) {
		&__img:before {
			padding-top: 440px;
		}
	}


	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		// image
		&__img {
			position: absolute;
			left: 50%;
			top: -1px;
			bottom: -1px;
			right: -1px;
			height: auto;
			overflow: hidden;
			margin: 0;
		}
		// content
		&__content-inner {
			padding: 70px 0 65px;
		}

		&__img ~ &__content {
			padding: 0;
			max-width: 50%;
		}
		&__img ~ &__content &__content-inner {
			padding: 30px 30px 30px 0;
		}

		// in row-main
		.row-main & &__content-inner {
			padding-left: 30px;
			&:before,
			&:after{
				left: 30px;
				right: 30px;
			}
		}
		.row-main & &__img {
			margin: 0;
		}

		// img left
		&--inverse{
			#{$s}{
				&__img{
					left: 0;
					right: 50%;
				}
			}
			.box-hero__content{
				margin-left: auto;
			}
			.box-hero__content .box-hero__content-inner {
				padding-left: 30px;
				padding-right: 0;
				&:before,
				&:after{
					left: 30px;
				}
			}
			.row-main & .box-hero__content .box-hero__content-inner {
				padding-right: 30px;
				&:before,
				&:after{
					right: 30px;
				}
			}
		}

		// VARIANTS
		&--size-s &__content-inner {
			height: 300px;
		}
		&--size-m &__content-inner {
			height: 450px;
		}
		&--size-l &__content-inner {
			height: 530px;
		}

		&--border{
			#{$s}__content-inner{
				&:before{
					top: 40px;
				}
				&:after{
					bottom: 40px;
				}
			}
		}
	}

	// MEDIA
	@media(min-width: $desktop){
		&__img ~ &__content &__content-inner {
			padding: 60px 70px 60px 0;
			&:before,
			&:after{
				right: 70px;
			}
		}
		.row-main & &__content-inner {
			padding-left: 70px;
			&:before,
			&:after{
				left: 70px;
				right: 80px;
			}
		}

		// img left
		&--inverse{
			.box-hero__content .box-hero__content-inner {
				padding-left: 70px;
				padding-right: 0;
				&:before,
				&:after{
					left: 70px;
				}
			}
			.row-main & .box-hero__content .box-hero__content-inner {
				padding-right: 70px;
				&:before,
				&:after{
					right: 70px;
				}
			}
		}
	}
}
