%btn{
	display: inline-block;
	vertical-align: middle;
	margin: 0;
	padding: 0;
	border: none;
	background: none;
	text-decoration: none;
	cursor: pointer;
}

/* BUTTON */
.btn {
	@extend %btn;
	> span {
		position: relative;
		display: block;
		padding: 14px 41px;
		font: bold 18px/28px $font;
		letter-spacing: 0.05em;
		text-decoration: none;
		&:not(.no-uppercase) {
			text-transform: uppercase;
		}
		text-align: center;
		transition: background .2s, color .2s, border-color .2s, box-shadow .2s;
	}
	.no-touch &:focus{
		&:not(.is-disabled):not(:disabled){
			> span{
				color: #fff;
				background: $color;
				border-color: $color;
			}
		}
	}

	&-secondary-font{
		> span{
			font-family: $fontMuni;
			letter-spacing: 0;
		}
	}

