			&-#{nth($customColor, 1)} {
				@include customButton(#{nth($customColor, 2)}, #{nth($customColor, 3)})
			}
		}
	}

	// btn block
	&-block{
		width: 100%;
		display: block;
	}

	//icon
	&-icon{
		@extend %btn;
		font-size: 16px;
		font-weight: bold;
		color: $muni;
		transition: color .2s;
		.no-touch &:focus{
			&:not(.is-disabled):not(:disabled){
				color: $color;
			}
		}
		@include hover() {
			&:hover {
				&:not(.is-disabled):not(:disabled){
					color: $color;
				}
			}
		}
	}

	// border
	&-border{
		> span{
			background: none;
		}
	}

	// sizes
	&-s{
		> span{
			font-size: 16px;
			line-height: 24px;
			padding: 8px 28px 8px;
		}
	}
	&-xs{
		> span{
			font-size: 16px;
			line-height: 24px;
			padding: 1px 28px 1px;
		}
	}

	//ikonky
	&-icon-before,
	&-icon-after{
		.icon{
			vertical-align: unset;
			position: relative;
		}
	}
	&-icon-before{
		.icon{
			margin-right: 5px;
		}
	}
	&-icon-after{
		.icon{
			margin-left: 5px;
		}
	}

	//btn dropdown
	&-dropdown{
		font-size: 14px;
		font-weight: bold;
		display: inline-block;
		vertical-align: top;
		position: relative;
		margin: 0 0 1.25em;
		.btn{
			.icon{
				font-size: 18px;
				top: 1px;
			}
		}
		&.is-open{
			.btn-dropdown__menu{
				opacity: 1;
				visibility: visible;
			}
		}
		&__menu{
			position: absolute;
			z-index: 10;
			background: #fff;
			width: 370px;
			border: 1px solid $borderPrimaryLight;
			padding: 15px 20px;
			visibility: hidden;
			opacity: 0;
			transition: opacity .2s;
			@media(max-width: $tablet - 1){
				top: 100%;
				margin-top: 10px;
				left: 0;
				max-width: calc(100vw - 30px);
			}
			@media(min-width: $tablet){
				&--right{
					left: 100%;
					top: 0;
					margin-left: 10px;
				}
				&--left{
					top: 0;
					right: 100%;
					margin-right: 10px;
				}
				&--up{
					left: 0;
					margin-bottom: 10px;
					bottom: 100%;
				}
				&--down{
					left: 0;
					margin-top: 10px;
					top: 100%;
				}
			}
			&--right-align {
				right: 0;
				left: auto;
			}
		}
		&__list{
			@extend %reset-ul;
		}
		&__item{
			@extend %reset-ul-li;
			border-top: 1px solid $borderPrimary;
			&:first-child{
				border: none;
			}
		}
		&__link{
			display: block;
			padding: 10px 15px;
			transition: background .2s;
			@include hover() {
				&:hover {
					background: $elementsHover;
					text-decoration: none;
				}
			}
			&.with-icon{
				padding-left: 40px;
				position: relative;
				.icon{
					position: absolute;
					left: 15px;
					top: 14px;
					font-size: 16px;
					color: $muni;
				}
			}
			.tag{
				float: right;
				margin-left: 5px;
				margin-top: 2px;
			}
		}
	}

	&.is-disabled,
	&:disabled{
		text-decoration: none;
		> span{
			color: #fff;
			background: $greyDark;
			border-color: $greyDark;
			cursor: default;
		}
	}

	&-wrap{
		font-size: 0px;
		margin: 0 0 0 -20px;
		> .btn,
		> .btn-dropdown{
			margin: 0 0 15px 20px;
		}
		&--wide {
			.btn {
				display: block;
			}
		}
	}
}
