$s: '.box-article';

.box-article{
	margin: 0 0 $mMarginSmall;
	font-size: 14px;
	display: block;
	@include clearfix();
	&__inner[href]{
		color: $color;
		text-decoration: none;
		@include hover() {
			&:hover {
				color: $color;
				#{$s}__title{
					color: $linkHover;
					text-decoration: underline;
				}
			}
		}
		&:visited{
			#{$s}__title{
				color: $linkVisited;
			}
		}
		@include hover() {
			&:visited:hover {
				#{$s}__title {
					color: $linkHover;
				}
			}
		}
	}
	&__img{
		text-align: center;
	}
	&__title{
		font-size: 18px;
		margin: 0 0 .8em;
		color: $link;
		position: relative;
		padding-bottom: 15px;
		transition: color .2s;
		-moz-transition: none;
		&:before{
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 40px;
			height: 2px;
			background: $color;
		}
	}
	&__desc{
		margin: 0;
	}

	@media(max-width: $mobileL - 1){
		&__img{
			margin: 0 0 10px;
		}
		.meta{
			margin: 0 0 5px;
		}
	}

	@media(max-width: $desktopWide - 1){
		&__img{
			img{
				min-width: 100%;
			}
		}
	}

	@media(min-width: $mobileL){
		&__img{
			float: left;
			width: 180px;
			margin: 0 15px 5px 0;
		}
	}

	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		padding-left: 285px;
		&__img{
			margin: 0 0 0 -285px;
			width: 270px;
		}
	}

	@media(min-width: $desktop){
		padding-left: 300px;
		&__img{
			margin: 0 0 0 -300px;
		}
	}
}
