.menu-subbox{
	z-index: 50;
	padding: 20px;
	transition: opacity .2s;
	display: none;
	.grid{
		margin-bottom: -20px;
		&__cell {
			margin-bottom: 20px;
			.grid {
				margin-bottom: 0;
				&__cell {
					margin-bottom: 0;
				}
			}
		}
	}
	&__list{
		@extend %reset-ul;
		font-size: 16px;
	}
	&__item{
		@extend %reset-ul-li;
		margin: 0;
	}
	&__link{
		display: block;
		padding: 6px 18px;
		text-decoration: none;
		position: relative;
		&:before{
			@extend %icon;
			color: #AAAAAA;
			content: $fa-var-angle-right;
			position: absolute;
			left: 0;
			top: 8px;
			font-size: 18px;
		}
	}
	&__close{
		display: none;
	}
	&__title{
		font-size: 18px;
		font-family: $fontSecondary;
		&__link{
			text-decoration: none;
		}
		&__toggle{
			display: none;
		}
	}
	td,
	th{
		padding: 3px 0;
	}
	&__subtitle{
		font-weight: bold;
		margin-bottom: 15px;
	}
	// TEMPORARY
	.box-article-grid{
		@include hover() {
			&:hover {
				.box-article__title{
					text-decoration: none !important;
				}
			}
		}
	}
	.box-article-grid__title{
		font-size: 16px;
		font-weight: normal;
		padding-bottom: 0px;
	}
	.box-article-grid__title:before{
		display: none;
	}
	.box-article-grid__content{
		padding: 10px 0 0;
	}
	a.text-icon .text-icon__label{
		font-size: 16px;
		text-decoration: none;
	}
	.text-icon{
		padding: 4px 0 4px 42px;
	}
	.text-icon__icon--bg{
		width: 32px;
		height: 32px;
		border-radius: 2px;
	}
	.text-icon__icon{
		line-height: 32px;
	}
	@media(min-width: $tablet) {
		position: absolute;
		left: 5px;
		right: 5px;
		top: 100%;
		margin: 0;
		padding: 30px 50px 40px;
		border: 2px solid #EEEEEE;
		border-radius: 2px;
		background: #fff;
		&__title {
			margin-bottom: 20px;
		}
	}
	@media(min-width: $desktop) {
		left: 15px;
		right: 15px;
	}
}
