$s: '.box-rotate';

.box-rotate{
	perspective: 1000px;
	transform-style: preserve-3d;
	margin: 0 0 $mMarginSmall;
	&.hover{
		#{$s}{
			&__front{
				transform: rotateY(180deg);
			}
			&__back{
				transform: rotateY(0deg);
			}
		}
	}
	@include hover() {
		&:hover {
			#{$s}{
				&__front{
					transform: rotateY(180deg);
				}
				&__back{
					transform: rotateY(0deg);
				}
			}
		}
	}
	&__flipper{
		transition: transform 0.6s;
		transform-style: preserve-3d;
		position: relative;
		&:before{
			content: "";
			display: block;
			padding-top: 100%;
		}
	}
	&__front,
	&__back{
		-webkit-backface-visibility: hidden;
		-moz-backface-visibility: hidden;
		-ms-backface-visibility: hidden;
		backface-visibility: hidden;
		transition: transform 0.6s;
		transform-style: preserve-3d;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__title{
		color: #fff;
	}
	&__front{
		z-index: 2;
		transform: rotateY(0deg);
		background: $primary;
	}
	&__back{
		transform: rotateY(-180deg);
		background: $primary;
		text-decoration: none;
		@include hover() {
			&:hover {
				text-decoration: none;
			}
		}
	}
	&__content{
		padding: 20px 30px;
		text-align: center;
		color: #fff;
		p:last-child{
			margin-bottom: 0;
		}
	}
	&__icon{
		font-size: 28px;
		margin-bottom: 19px;
	}

	&--front-bg{
		#{$s}{
			&__front{
				background-size: cover;
				background-position: center center;
				background-repeat: no-repeat;
			}
		}
	}

	&--back-bg{
		#{$s}{
			&__back{
				background-size: cover;
				background-position: center center;
				background-repeat: no-repeat;
			}
		}
	}

	&--front-overlay{
		#{$s}{
			&__front{
				&:before{
					content: "";
					position: absolute;
					top: 0;
					left: 0;
					bottom: 0;
					right: 0;
					background: rgba($color, .4);
					z-index: -1;
				}
			}
		}
	}

	&--back-overlay{
		#{$s}{
			&__back{
				&:before{
					content: "";
					position: absolute;
					top: 0;
					left: 0;
					bottom: 0;
					right: 0;
					background: rgba($color, .4);
					z-index: -1;
				}
			}
		}
	}

	&--front-text{
		#{$s}{
			&__front{
				#{$s}{
					&__content{
						color: $color;
					}
				}
			}
		}
	}

	&--back-text{
		#{$s}{
			&__back{
				color: $color;
				#{$s}{
					&__content{
						color: $color;
					}
					&__title{
						color: $color;
					}
				}
			}
		}
	}

	@media (min-width: $tablet) {
		margin: 0 0 $mMargin;
	}
}
