$s: '.box-slider';

.box-slider {
	position: relative;
	z-index: 1;
	margin: 0 0 $mMarginSmall;
	@include hover() {
		&:hover {
			#{$s}__prev,
			#{$s}__next {
				color: #fff;
				opacity: .6;
				&:hover {
					opacity: 1;
				}
			}
			#{$s}__play {
				.icon {
					border-color: #fff;
				}
			}
		}
	}
	.image {
		margin: 0;
		img {
			min-width: 100%;
		}
	}
	&__list {
		white-space: nowrap;
		font-size: 0;
		overflow: hidden;
		overflow-x: auto;
		> * > * {
			white-space: normal;
		}
		.js & {
			overflow-x: hidden;
		}
	}
	&__item {
		display: inline-block;
		vertical-align: top;
		width: 100%;
		font-size: $fontSize;
		text-decoration: none;
		&[href]{
			&:visited{
				#{$s}__title {
					color: $linkVisited;
				}
			}
		}
		img {
			max-width: 100%;
			height: auto;
		}
		> a {
			display: block;
			text-decoration: none;
			@include hover() {
				&:hover {
					#{$s}__title {
						text-decoration: underline;
					}
				}
			}
		}
		&[data-desc],
		&[data-title] {
			.image:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				height: 200px;
				z-index: 101;
				background-image: linear-gradient(to top, rgba(0, 0, 0, .7), rgba(0, 0, 0, 0));
			}
		}
		&--video[data-desc],
		&--video[data-title],
		&--video {
			.image:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				height: 100%;
				z-index: 101;
				background: rgba($muni, .7);
			}
		}
	}
	&__wrap {
		position: relative;
		margin-bottom: 16px;
	}
	&__prev,
	&__next {
		width: 54px;
		height: 54px;
		color: #fff;
		text-decoration: none;
		text-align: center;
		font-size: 56px;
		position: absolute;
		top: 50%;
		margin-top: -28px;
		z-index: 200;
		opacity: 0;
		transition: opacity .2s;
		@include hover() {
			&:hover {
				color: #fff;
				opacity: 1;
			}
		}
		.touch & {
			opacity: .7;
		}
		.icon {
			display: block;
		}
		&.is-disabled{
			display: none;
		}
	}
	&__prev {
		left: 0;
	}
	&__next {
		right: 0;
	}

	&__inner {
		display: block;
		position: relative;
		text-decoration: none;
	}

	&__overlay {
		position: static;
		color: $color;
		&.is-clickable{
			cursor: pointer;
		}
	}
	&__title {
		margin: 0;
		font-size: 18px;
		line-height: (24/18) + em;
		color: $muni;
		-moz-transition: none;
	}

	&--small {
		#{$s}__wrap {
			margin-bottom: 16px;
		}
		.slick-dots {
			position: static;
			text-align: left;
			padding-top: 25px;
			li{
				&.slick-active{
					button{
						background: $muni;
						border-color: $muni;
					}
				}
			}
			button{
				border-color: $border;
			}
		}
		#{$s}__overlay {
			position: static;
			color: $color;
			max-width: 100%;
		}
		#{$s}__title {
			color: $muni;
		}
		#{$s}__prev {
			left: 0;
		}
		#{$s}__next {
			right: 0;
		}
		#{$s}__item {
			&[data-desc],
			&[data-title] {
				.image:after {
					display: none;
				}
			}
		}
	}
	&--video {
		#{$s}__inner {

		}
		#{$s}__play {
			.icon {
				margin-bottom: 10px;
			}
		}
	}
	&--multiple {
		#{$s}__list {
			margin-left: -#{$mMarginSmall};
		}
		#{$s}__item {
			padding-left: $mMarginSmall;
		}
	}
	&--carousel,
	&--multiple,
	&--special {
		@include hover() {
			&:hover {
				#{$s}__prev,
				#{$s}__next {
					opacity: 1;
					color: $muni;
				}
			}
		}
		#{$s}__wrap {
			overflow: hidden;
			margin-bottom: 0;
		}
		#{$s}__item {
			width: 100%;
			vertical-align: top;
			white-space: normal;
			&--video {
				#{$s}__img {
					position: relative;
					&:before {
						content: '';
						position: absolute;
						top: 0;
						bottom: 0;
						left: 0;
						right: 0;
						background-image: linear-gradient(to top, rgba(0, 0, 0, .7), rgba(0, 0, 0, 0));
					}
					&:after {

					}
				}
			}
		}
		#{$s}__play {
			margin-top: -24px;
			.icon {
				width: 48px;
				height: 48px;
				line-height: 48px;
				font-size: 22px;
			}
		}
		#{$s}__pager {
			display: none;
		}
		#{$s}__inner {
			&:before {
				display: none;
			}
		}
		#{$s}__title {
			color: $muni;
		}
		#{$s}__content {
			padding-top: 20px;
			color: $color;
			font-size: 16px;
			line-height: (20/16) + em;
		}
		#{$s}__prev,
		#{$s}__next {
			opacity: 1;
			color: $muni;
			transition: color .2s;
			@include hover() {
				&:hover {
					color: $color;
					opacity: 1;
				}
			}
		}
		#{$s}__prev {
			left: -65px;
		}
		#{$s}__next {
			right: -65px;
		}
	}
	&--special {
		@include hover() {
			&:hover {
				#{$s}__prev,
				#{$s}__next {
					opacity: 1;
					color: $muni;
				}
			}
		}
		#{$s}__wrap {
			overflow: visible;
			margin-bottom: 0;
		}
		#{$s}__prev,
		#{$s}__next {
			opacity: 1;
			color: $muni;
			transition: color .2s;
			@include hover() {
				&:hover {
					color: $color;
					opacity: 1;
				}
			}
		}
		#{$s}__prev {
			left: -65px;
		}
		#{$s}__next {
			right: -65px;
		}
	}
	&--carousel{
		.slick-slide{
			flex: 0 0 33.333%;
		}
	}
	@media (min-width: $tablet) {
		margin: 0 0 $mMargin;
	}
	@media(max-width: 1239px) {
		&--carousel,
		&--multiple,
		&--special{
			#{$s}__wrap{
				padding-bottom: 50px;
			}
			#{$s}__prev,
			#{$s}__next {
				top: auto !important;
				bottom: 0;
			}
			#{$s}__prev {
				left: auto;
				right: 50%;
			}
			#{$s}__next {
				right: auto;
				left: 50%;
			}
		}
	}

	@media(min-width: $tablet) {
		&__wrap {
			margin-bottom: 0;
		}
		&__overlay {
			position: absolute;
			bottom: 45px;
			left: 30px;
			right: 30px;
			z-index: 100;
			color: #fff;
			font-size: 16px;
			line-height: (20/16) + em;
			z-index: 200;
			//max-width: 50%;
			> *:last-child {
				margin-bottom: 0;
			}
		}
		&__title {
			color: #fff;
			margin-bottom: 4px;

		}
		&__prev {
			left: 10px;
		}
		&__next {
			right: 10px;
		}
		&--video {
			#{$s}__overlay {
				text-align: center;
			}
		}
		&--carousel,
		&--multiple {
			#{$s}__item{
				width: 33.333%;
			}
		}
		&--special {
			.slick-list {
				padding-bottom: 30px;
			}
			.slick-dots {
				bottom: 0;
			}
		}
	}

	@media(min-width: $desktop) {
		&--carousel,
		&--multiple {
			#{$s}__item{
				width: 25%;
			}
		}
	}
}
