.box-content {
	margin: 0 0 $mMarginSmall;
	max-width: 800px;
	-moz-transition: none;
	&--narrow {
		max-width: 590px;
	}
	> .row-main > :last-child,
	> :last-child {
		margin-bottom: 0;
	}
	a {
		-moz-transition: none;
	}
	a:visited{
		color: $linkVisited;
	}
	@include hover() {
		&:visited:hover {
			color: $linkHover;
		}
	}
	@media (min-width: $tablet) {
		margin: 0 0 $mMargin;
	}
}
