.box-map {
	position: relative;
	margin: 0 0 $mMarginSmall;
	height: 400px;
	&__placeholder {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: $greyLight;
		text-align: center;
		&:before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			height: 100%;
			margin-right: -.25em;
		}
	}
	&__inner {
		display: inline-block;
		vertical-align: middle;
		color: $color;
	}
	&__text {
		@extend .h3;
		margin: 0;
		color: $light;
	}
	&__icon {
		@extend .h4;
		margin-bottom: .25em;
		color: $light;
		font-size: 40px;
	}
	&__map {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	@media (max-height: 400px){
		height: 80vh;
	}

	@media (min-width: $tablet) {
		margin: 0 0 $mMargin;
	}
}
