$s: '.crossroad-links';

.crossroad-links{
	margin: 0 0 $mMarginSmall;
	&__title{
		margin: 0 0 .75em;
	}
	&__list{
		@extend %reset-ul;
		font-size: 16px;
		margin: 0 0 $mMarginSmall;
		#{$s}{
			&__list{
				margin: .75em 0 0;
			}
			&__list.small{
				margin: .2em 0 0;
			}
		}
	}
	&__item{
		@extend %reset-ul-li;
		display: inline-block;
		vertical-align: top;
		width: 100%;
		padding: 0 0 0 20px;
		margin: 0 0 .35em;
		position: relative;
		.small & {
			margin: 0 0 .2em;
		}
		&:before{
			@extend %icon;
			color: $muni;
			content: $fa-var-angle-right;
			position: absolute;
			left: 0;
			top: .35em;
			font-size: 14px;

			.small &{
				top: .25em;
			}
		}
	}
	&__subtitle{
		font-size: 16px;
		margin: 0 0 .5em;
		a{
			-moz-transition: none;
			&:visited{
				color: $linkVisited;
			}
			@include hover() {
				&:visited:hover {
					color: $linkHover;
				}
			}
		}
	}
	&__links{
		margin: 0 0 2px -15px;
		a{
			margin-left: 15px;
		}
	}
	&__desc{
		.table-vcard{
			margin: 0 0 1.25em;
			font-size: 14px;
			td{
				color: $color;
				border-color: transparent;
			}
		}
	}
	&__link{
		-moz-transition: none;
		&:visited{
			color: $linkVisited;
		}
		@include hover() {
			&:visited:hover {
				color: $linkHover;
			}
		}
	}

	&--type-2{
		#{$s}__desc{
			display: block;
			font-size: 14px;
		}
	}

	&--type-3{
		border: 1px solid $border;
		border-width: 1px 0;
		padding: 22px 15px 17px;
		#{$s}__list{
			font-size: 14px;
		}
		#{$s}__item{
			padding-left: 16px;
		}
		#{$s}__item:before{
			top: 4px;
		}
		#{$s}__link{
			color: $muni;
			text-decoration: none;
		}
	}

	&--type-4{
		#{$s}__item{
			padding-left: 0;
			margin: 0 0 1em;
			&:before{
				display: none;
			}
		}
		#{$s}__subtitle{
			margin: 0 0 5px;
		}
		#{$s}__desc{
			display: block;
			font-size: 14px;
			margin: 0;
		}
	}

	&--background {
		background: $muni;
		padding: 13px 15px 10px;
		-webkit-font-smoothing: subpixel-antialiased;
		#{$s}__link{
			color: #fff;
			font-weight: bold;
			text-decoration: none;
			@include hover() {
				&:hover {
					text-decoration: underline;
					color: #fff;
				}
			}
		}
		#{$s}__item{
			padding-left: 16px;
			&:before {
				top: 5px;
				color: $whiteInvertable;
			}
		}
	}

	&--size--auto {
		#{$s}__list{
			margin: 0 0 0 -20px;
		}
		#{$s}__item{
			width: auto;
			margin-left: 20px;
		}
	}

	&--size--xs--1-2{
		> #{$s}__list{
			@include column-gap(20px);
		}
	}
	&--size--xs--1-2{
		> #{$s}__list{
			@include column-count(2);
		}
	}

	@media(min-width: $mobileL){
		&--size--s--1-2{
			> #{$s}__list{
				@include column-gap(20px);
			}
		}
		&--size--s--1-2{
			> #{$s}__list{
				@include column-count(2);
			}
		}
	}

	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		&--size--auto {
			#{$s}__list{
				margin-left: -40px;
			}
			#{$s}__item{
				margin-left: 40px;
			}
		}
		&--size--m--1-4,
		&--size--m--2-4{
			> #{$s}__list{
				@include column-gap(20px);
			}
		}
		&--size--m--1-4{
			> #{$s}__list{
				@include column-count(4);
			}
		}
		&--size--m--2-4{
			> #{$s}__list{
				@include column-count(2);
			}
		}
	}

	@media(min-width: $desktop){
		&--size--l--2-12,
		&--size--l--3-12,
		&--size--l--4-12,
		&--size--l--6-12{
			> #{$s}__list{
				@include column-gap(30px);
			}
		}
		&--size--l--2-12{
			> #{$s}__list{
				@include column-count(6);
			}
		}
		&--size--l--3-12{
			> #{$s}__list{
				@include column-count(4);
			}
		}
		&--size--l--4-12{
			> #{$s}__list{
				@include column-count(3);
			}
		}
		&--size--l--6-12{
			> #{$s}__list{
				@include column-count(2);
			}
		}
	}
}
