$s: '.box-event';

.box-event {
	margin: 0 0 $mMarginSmall;
	.datebox {
		margin-bottom: 12px;
	}
	&__title {
		margin-top: 0;
		font-size: 18px;
		line-height: (22/18);
		position: relative;
		padding-bottom: 15px;
		margin-bottom: 15px;
		color: $muni;
		-moz-transition: none;
		&:before{
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 35px;
			height: 2px;
			background: $color;
		}
		a{
			-moz-transition: none;
			display: block;
			&:visited{
				color: $linkVisited;
			}
		}
		@include hover() {
			a:hover {
				text-decoration: underline;
			}
		}
		@include hover() {
			a:visited:hover {
				color: $linkHover;
			}
		}
	}
	&__label {
		display: inline-block;
		vertical-align: top;
		padding: 3px 12px 4px 11px;
		background: $muni;
		color: $whiteInvertable;
		font-size: $fontSizeSmaller;
		font-weight: 700;
		&-wrap {
			margin-top: (12em/16);
		}
	}
	&__content{
		font-size: 14px;
		line-height: 1.45;
		text-decoration: none;
		color: $color;
		display: block;
		> :last-child{
			margin-bottom: 0;
		}
		&:visited #{$s}__title{
			color: $linkVisited;
		}
		&[href]{
			@include hover() {
				&:hover #{$s}__title {
					text-decoration: underline;
					color: $color;
				}
			}
		}
	}

	&--side {
		padding-top: 0;
		.datebox {
			float: left;
			margin-top: 0;
			margin-left: -100px;
			margin-bottom: 0;
			text-align: right;
		}
		#{$s}__title {
			& ~ * {
				margin-bottom: 2px;
			}
		}
		#{$s}__content {
			padding-left: 100px;
			padding-bottom: 0;
			font-size: 16px;
			&:after {
				content: '';
				display: table;
				clear: both;
			}
		}
		&#{$s}--highlighted {
			padding-top: 0;
			#{$s}__content {
				padding: 15px 25px 20px 100px;
			}
		}
	}

	&--highlighted {
		#{$s}__content {
			background: $muni;
			color: $whiteInvertable;
			padding: 20px 25px 30px 30px;
			@include hover() {
				&:hover {
					color: #fff;
				}
			}
			&[href]{
				@include hover() {
					&:hover #{$s}__title {
						color: #fff;
					}
				}
			}
			&:visited #{$s}__title{
				color: #fff;
				text-decoration: underline;
			}
			a {
				color: $whiteInvertable;
				@include hover() {
					&:hover {
						text-decoration: none;
					}
				}
			}
			#{$s}__title a {
				@include hover() {
					&:hover {
						color: #fff;
						text-decoration: underline;
					}
				}
			}
		}
		#{$s}__title {
			color: #fff;
			&:before{
				background: #fff;
			}
		}
	}
	@media (min-width: $tablet) {
		margin: 0 0 $mMargin;
	}
}
