.box-product-attrs {
	font-family: $fontMuni;
	font-weight: bold;
	color: $muni;
	margin: 0 0 30px;
	&__list {
		@extend %reset-ul;
		@include inline-list(auto, 13px);
		margin: 0 0 -10px -10px;
	}
	&__item {
		min-width: 32px;
		@extend %reset-ul-li;
		text-transform: uppercase;
		background: rgba($muni, .1);
		border: 1px solid $muni;
		text-align: center;
		margin: 0 0 10px 10px;
		padding: 0 5px;
		font-size: 16px;
		height: 32px;
		line-height: 32px;
		&--color {
			@include text-hide();
			border-width: 0px;
		}
		&--border {
			border-width: 1px;
		}
	}
}
