$s: '.menu-lang';

.menu-lang{
	margin: 0;
	font-size: 14px;
	white-space: nowrap;
	position: relative;
	display: none;
	&__selected{
		font-size: 0px;
		&__link{
			display: inline-block;
			vertical-align: top;
			margin-left: 15px;
			font-size: 14px;
			text-decoration: none;
		}
		&__link{
			@include hover() {
				&:hover {
					text-decoration: none;
				}
			}
		}
	}
	&--select{
		z-index: 500;
		#{$s}{
			&__selected{
				&__link{
					padding: 0 35px 0 17px;
					position: relative;
					margin-right: -15px;
					border: 1px solid transparent;
					border-bottom: 0;
					&:before {
						@extend %icon;
						content: $fa-var-angle-down;
						position: absolute;
						top: 50%;
						right: 15px;
						margin-top: -6px;
						font-size: 14px;
						text-align: center;
						color: $muni;
						transition: transform .2s, color .2s;
					}
					&:after{
						content: '';
						position: absolute;
						left: 0;
						right: 0;
						bottom: 0;
						background: #fff;
						height: 8px;
						z-index: 10;
						visibility: hidden;
						opacity: 0;
						transition: opacity .2s;
					}
					@include hover() {
						&:hover {
							&:before{
								color: $color;
							}
						}
					}
				}
			}
			&__popup{
				position: absolute;
				right: -15px;
				top: 100%;
				width: 150px;
				background: #fff;
				visibility: hidden;
				opacity: 0;
				transition: opacity .2s;
				margin-top: -1px;
				border: 1px solid rgba($color, .2);
				padding: 0 20px;
				font-size: 16px;
				&__link{
					display: block;
					border-top: 1px solid $border;
					text-decoration: none;
					transition: color .2s, background .2s;
					padding: 4px 0;
					&.is-active{
						color: $dark;
					}
					@include hover() {
						&:hover {
							color: $dark;
						}
					}
					&:first-child{
						border: 0;
					}
				}
			}
		}
		&.is-hover{
			#{$s}{
				&__selected{
					&__link{
						background: #fff;
						border: 1px solid rgba($color, .2);
						border-bottom: 0;
						&:before{
							transform: rotate(180deg);
						}
						&:after{
							visibility: visible;
							opacity: 1;
						}
					}
				}
				&__popup{
					opacity: 1;
					visibility: visible;
				}
			}
		}
		&-dark{
			#{$s}{
				&__selected{
					&__link{
						&:after{
							background: $muni;
						}
					}
				}
				&__popup{
					background: $muni;
					&__link{
						color: #fff;
						border-color: rgba(#fff,.15);
						&.is-active{
							color: #fff;
							background: darken($muni, 5%);
						}
						@include hover() {
							&:hover {
								color: #fff;
								background: darken($muni, 5%);
							}
						}
					}
				}
			}
			&.is-hover{
				#{$s}{
					&__selected{
						&__link{
							background: $muni;
							color: #fff;
						}
					}
				}
			}
		}
	}

	@media(max-width: $tablet - 1){
		&--select{
			line-height: 42px;
			margin-top: -10px;
		}
	}

	@media(min-width: $tablet){
		display: block;
		line-height: 42px;
		&__selected{
			&__link{
				font-size: 18px;
			}
		}
	}
}
