.box-code{
	margin: 0 0 $mMarginSmall;
	&__title{
		background: $muni;
		color: #fff;
		padding: 0 46px 0 46px;
		position: relative;
		line-height: 30px;
		font-weight: bold;
		font-size: 14px;
		&__icon,
		&__close{
			position: absolute;
			left: 20px;
			top: 50%;
			font-size: 16px;
			margin-top: -9px;
			color: #fff;
			text-decoration: none;
		}
		&__close{
			left: auto;
			right: 20px;
			transition: opacity .2s;
			@include hover() {
				&:hover {
					opacity: .75;
					color: #fff;
				}
			}
		}
	}
	&__inner{
		margin: 0;
		background: rgba($muni, .05);
		border-left: 2px solid $muni;
		padding: 16px 26px;
		font-size: 14px;
		font-family: $fontConsola;
		overflow: hidden;
		overflow-x: auto;
		-webkit-overflow-scrolling: touch;
	}
	&__content{
		margin: 0;
		background: rgba($muni, .05);
		border-left: 2px solid $muni;
		padding: 20px 20px 3px;
		font-size: 14px;
		p {
			margin-bottom: 1.5em;
		}
	}

	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		margin-left: $mMarginSmall;
	}
}
