// List Icons
// -------------------------

.#{$fa-css-prefix}-ul {
	font-size: 14px;
	> li {
		@extend %reset-ul-li;
		position: relative;
		margin: 0 0 .3em;
		padding: 0 0 0 25px;
	}
	ul{
		margin-top: 1em;
		margin-bottom: 0;
	}
}
.#{$fa-css-prefix}-li {
	position: absolute;
	left: 0;
	top: 50%;
	color: $primary;
}
.#{$fa-css-prefix}-li.#{$fa-css-prefix}-li{
	margin-top: -(7/14)+em;
}
// SPEC
.#{$fa-css-prefix}-ul--spec{
	margin: 0;
	font-size: 16px;
	.#{$fa-css-prefix}-li.#{$fa-css-prefix}-li{
		top: 5px;
		margin-top: 0;
		color: #AAAAAA;
	}
	a{
		text-decoration: none;
	}
	> li{
		padding: 2px 0 2px 30px;
	}
}


.big .#{$fa-css-prefix}-ul,
.big.#{$fa-css-prefix}-ul {
	font-size: 22px;
	margin-left: 0;
	margin-bottom: 30px;
	> li {
		padding-left: 38px;
		margin-bottom: .65em;
	}
}

.big .#{$fa-css-prefix}-li {
	font-size: 28px;
	top: 12px;
}
