.inp{
	&-text{
		display: block;
		border: 1px solid $input;
		padding: 11px 15px;
		background: #fff;
		color: $color;
		font: normal 16px/20px $font;
		border-radius: 0;
		@include appearance(none);
		@include transition(color .2s, border-color .2s, background .2s);
		@include placeholder {
			color: #595959;
			opacity: 1;
			@include transition(color .2s);
		}
		&:focus{
			border-color: $muni;
			@include placeholder {
				color: rgba($color, .3);
			}
		}
		&:disabled{
			border-color: rgba($color, .2);
			color: rgba($color, .2);
		}
		.error &{
			border-color: $red !important;
		}
		&--big {
			height: 60px;
			padding: 19px 20px;
		}
		&--small {
			padding-top: 4px;
			padding-bottom: 4px;
			font-size: 14px;
		}
	}

	// fix
	&-fix{
		display: block;
		position: relative;
		label + br + &{
			margin-top: .35em;
		}
		label.vhide + &{
			margin-top: 0;
		}
		.inp-text{
			width: 100%;
		}
	}

	// info
	&-info{
		font-size: 14px;
		display: block;
		padding: 8px 0 0;
		text-align: right;
		.error &{
			color: $red;
		}
		.success &{
			color: $green;
		}
		.warning &{
			color: $orange;
		}
	}

	// item
	&-item{
		font-size: 16px;
		display: inline-block;
		vertical-align: top;
		position: relative;
		font-weight: normal;
		padding-left: 29px;
		cursor: pointer;
		input{
			position: absolute;
			top: 0;
			left: -50000px;
			.no-touch &:focus + span:before{
				border-color: $muni;
			}
			&:checked + span:before{
				border-color: $muni;
			}
			.white &:checked + span:before{
				border-color: #fff;
			}
			&:checked + span:after{
				opacity: 1;
			}
			&:disabled + span{
				color: $input;
				cursor: default;
				&:before{
					border-color: $input;
					box-shadow: none;
				}
			}
		}
		> span{
			display: block;
			&:before,
			&:after{
				content: '';
				position: absolute;
				left: 0;
				top: 2px;
			}
			&:before{
				border: 1px solid #595959;
				width: 20px;
				height: 20px;
				@include transition(border-color .2s);
			}
			.white &:before{
				border: 2px solid #fff;
			}
			&:after{
				opacity: 0;
				@include transition(opacity .2s);
			}
		}
		&--checkbox{
			input{
				&:disabled + span{
					&:after{
						color: $input;
					}
				}
			}
			> span{
				&:after{
					@extend %icon;
					position: absolute;
					top: 3px;
					left: 0;
					width: 22px;
					height: 22px;
					line-height: 20px;
					font-size: 11px;
					text-align: center;
					color: $muni;
					content: $fa-var-check;
				}

				.white &:after{
					color: #fff;
					top: 2px;
					left: -1px;
					font-weight: 800;
				}
			}
		}
		&--radio{
			input{
				&:disabled + span{
					&:after{
						background: $input;
					}
				}
			}
			> span{
				&:before,
				&:after{
					border-radius: 50%;
					width: 22px;
					height: 22px;
				}
				&:after{
					width: 10px;
					height: 10px;
					top: 8px;
					left: 6px;
					background: $muni;
				}
				.white &:after {
					background: #fff;
				}
			}
		}
		&--standalone {
			padding-left: 0;
			width: 24px;
			height: 24px;
			> span {
				position: relative;
				margin-left: 2px;
				height: 100%;
			}
		}
		.error &{
			color: $red;
			input{
				&:checked + span:before{
					border-color: $red;
				}
				.no-touch &:focus + span:before{
					border-color: $red;
				}
			}
			> span{
				&:before{
					border-color: $red;
					box-shadow: none;
				}
			}
			&--checkbox{
				> span{
					&:after{
						color: $red;
					}
				}
			}
			&--radio{
				> span{
					&:after{
						background: $red;
					}
				}
			}
		}
	}

	// inp items
	&-items{
		margin: 0 0 .75em;
		&__label{
			@extend label;
			margin: 0 0 .5em;
		}
		&__list{
			@extend %reset-ul;
			overflow: hidden;
		}
		&__item{
			@extend %reset-ul-li;
			margin: 0 0 .5em;
		}
		&--dropdown {
			padding: 11px 0;
			margin-bottom: 0;
			.inp-items {
				&__item {
					margin-bottom: 6px;
					&:last-child {
						margin-bottom: 0;
					}
				}
			}
			.inp-item {
				display: block;
				font-size: 14px;
				line-height: (22/14);
				padding-left: 39px;
				padding-right: 10px;
				> span::before {
					display: none;
				}
				&--radio > span::after {
					top: 6px;
    				left: 16px;
				}
				&--checkbox > span::after {
					top: 1px;
					left: 10px;
				}
				.no-touch &:hover {
					text-decoration: underline;
				}
			}
		}
		&--inline{
			.inp-items{
				&__list{
					@include inline-list(auto, $fontSize);
					margin-left: -30px;
				}
				&__item{
					margin: 0 0 .5em 30px;
				}
			}
		}
	}

	// icon
	&-icon{
		.icon{
			position: absolute;
			top: 50%;
			margin-top: -8px;
			font-size: 16px;
		}
		&--after{
			.icon{
				right: 14px;
			}
			.inp-text{
				padding-right: 42px;
			}
		}
		&--before{
			.icon{
				left: 14px;
			}
			.inp-text{
				padding-left: 42px;
			}
		}
	}
}

// textarea
textarea.inp-text{
	height: auto;
}

// label
label{
	font-size: 16px;
	font-weight: bold;
	.required{
		font-weight: normal;
		font-size: 14px;
		&--asterisk {
			font-weight: bold;
			font-size: 22px;
			line-height: 1;
			color: $red;
		}
	}
}

//fix
select::-ms-expand {
	display: none;
}
