$s: '.box-accordion';

.box-accordion{
	margin: 0 0 $mMarginSmall;
	border: 1px solid $border;
	border-width: 1px 0 0;
	&__item{
		border-bottom: 1px solid $border;
		&.is-open{
			> #{$s}__title{
				.icon{
					&:before{
						content: $fa-var-angle-up;
					}
				}

				> #{$s}__title__link{
					color: $color;
				}
			}
			> #{$s}__box{
				display: block;
			}
		}
	}
	&__title{
		margin: 0;
		font-size: 0px;
		line-height: 60px;
		position: relative;
		&__link{
			text-decoration: none;
			display: block;
			padding: 5px 90px 5px 20px;
			transition: color .2s, background .2s;
			@include hover() {
				&:hover {
					background: $elementsHover;
				}
			}
		}
		&__name{
			display: inline-block;
			vertical-align: middle;
			font-size: 16px;
			line-height: 24px;
		}
		.icon{
			position: absolute;
			right: 0;
			top: 0;
			bottom: -1px;
			width: 70px;
			font-size: 0px;
			color: $muni;
			&:before{
				position: absolute;
				left: 50%;
				top: 50%;
				margin: -12px 0 0 -8px;
				font-size: 28px;
			}
		}
	}
	&__box{
		position: relative;
		overflow: hidden;
		.js &{
			display: none;
		}
	}
	&__inner{
		font-size: 16px;
		padding: 12px 20px;
        overflow: auto;
        > :last-child{
        	margin-bottom: 0;
        }
	}
	// styly pro vice zanoreni
	&__item{
		#{$s}__item{
			#{$s}__title__name{
				font-size: 14px;
			}
			#{$s}__title__link{
				padding-left: 10px;
			}
			#{$s}__inner{
				font-size: 14px;
				padding-left: 10px;
				padding-right: 10px;
			}
		}
	}

	&--secondary{
		border-color: $color;
		border-width: 2px 0 0;
		#{$s}__item{
			border-bottom: 2px solid $color;
		}
	}

	&--bordered{
		border-width: 1px 1px 0;
		#{$s}__title{
			.icon{
				border: 1px solid $border;
				border-width: 0 0 1px 1px;
			}
		}
	}

	&--bordered-inside {
		border: 0;
		#{$s}__title{
			border-bottom: 1px solid #ccd4e4;
			.icon{
				border-left: 1px solid $border;
				bottom: 0;
			}
		}
		#{$s}__item {
			border: 0;
			&:not(:first-child){
				border-top: 1px solid #ccd4e4;
				margin-top: -1px;
			}
			&:last-child {
				margin-bottom: -1px;
			}
		}
	}

	&--small {
		#{$s}__title {
			line-height: 30px;
			.icon {
				width: 40px;
				&::before {
					font-size: 14px;
					line-height: (20/14);
					margin: -8px 0 0 -4px;
				}
			}
		}
		#{$s}__title__link {
			padding: 6px 60px 4px 20px;
		}
		#{$s}__title__name {
			font-size: 14px;
			line-height: (20/14);
		}
		#{$s}__inner {
			padding: 20px;
		}
	}

	&--white{
		background: #fff;
	}

	@media (min-width: $mobileL) {
		&--small {
			#{$s}__inner {
				padding: 30px;
			}
		}
	}
	@media(min-width: $tablet){
		margin: 0 0 $mMargin;
		&__title{
			&__link{
				padding: 5px 90px 5px 30px;
			}
		}
		&__name{
			font-size: 18px;
		}
		&__inner{
			padding: 12px 30px 40px;
		}
	}

	@media(min-width: $desktop){
		&--big{
			#{$s}__title{
				&__name{
					font-size: 24px;
					line-height: 30px;
				}
			}
		}
	}
}
