$s: '.modal';

.modal {
	.fancybox-close-small {
		display: none;
	}
	&--full {
		width: 100%;
	}
	&__content {
		position: relative;
		min-height: 100%;
		z-index: 1001;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__wrap {
		width: 100%;
		max-width: 970px !important;
		margin: 20px auto;
		font-size: 14px;
		.grid__cell {
			font-size: 14px;
		}
	}
	&__header {
		background-color: $blue;
		color: #fff;
		padding: 17px 94px 17px 30px;
		position: relative;
	}
	&__title {
		color: inherit;
		font-size: 18px;
		margin: 0;
		.text-icon__icon {
			color: inherit;
		}
	}
	&__close {
		color: inherit;
		position: absolute;
		top: 13px;
		right: 30px;
		font-size: 26px;
		line-height: 1;
		width: 34px;
		height: 34px;
		display: flex;
		align-items: center;
		justify-content: center;
		text-decoration: none;
		.icon {
			vertical-align: top;
		}
		@include hover() {
			&:hover {
				color: $greyDark;
			}
		}

		#{$s}--notitle & {
			top: 25px;
		}
	}
	&__body {
		padding: 20px;
		background-color: $greyLight;
		border: solid $borderPrimary;
		border-width: 0 1px 1px 1px;
		position: relative;
		> :last-child {
			margin-bottom: 0;
		}

	}
	&__footer {
		padding: 20px;
		background-color: #fff;
		border: solid $borderPrimary;
		border-width: 0 1px 1px 1px;
	}

	@media (max-width: $tablet - 1) {
		&__footer {
			.grid {
				margin-bottom: -20px;
			}
			.grid__cell {
				text-align: center;
				margin-bottom: 20px;
			}
		}
		&.fancybox-content {
			padding: 15px;
		}
		&--notitle &__body {
			padding-top: 55px;
			#{$s}__close {
				top: 13px;
				right: 13px;
			}
		}
	}

	@media (min-width: $tablet) {
		&__body {
			padding: 30px 70px 70px;

			#{$s}--notitle & {
				padding-top: 70px;
			}
		}
		&__footer {
			padding: 20px 70px;
		}
	}
}
