$s: '.tag';

.tag {
	display: inline-block;
	vertical-align: unset;
	font-family: $fontMuni;
	font-size: 16px;
	line-height: 20px;
	font-weight: bold;
	color: $whiteInvertable;
	background: $muni;
	padding: 3px 8px;
	&__remove {
		margin-left: 3px;
		color: #fff;
		@include hover() {
			&:hover {
				color: #ddd;
			}
		}
	}

	&--flat {
		font-family: $font;
		font-size: 14px;
		border-radius: 0;
		padding: 4px 15px 6px 12px;
		margin: 0 5px 0 12px;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: -12px;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 15px 12px 15px 0;
			border-color: transparent $muni transparent transparent;
		}
	}
	&--white {
		background: #fff;
		border: solid $border;
		border-width: 1px 1px 1px 0;
		color: $dark;
		padding-top: 4px;
		padding-bottom: 4px;
		&:before {
			top: -1px;
			border-color: transparent $border transparent transparent;
		}
		&:after {
			content: '';
			position: absolute;
			top: -1px;
			left: -11px;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 15px 12px 15px 0;
			border-color: transparent #fff transparent transparent;
		}
		#{$s} {
			&__remove {
				color: $color;
				@include hover() {
					&:hover {
						color: $muni;
					}
				}
			}
		}
	}

	&--primary {
		background: $primary;
		color: $primaryColor;
	}
	&--muni {
		background: $muni;
		color: #fff;
	}
	&--law {
		background: $law;
		color: #fff;
	}
	&--med {
		background: $med;
		color: #fff;
	}
	&--sci {
		background: $sci;
		color: #fff;
	}
	&--fi {
		background: $fi;
		color: $color;
	}
	&--fss {
		background: $fss;
		color: #fff;
	}
	&--fsps {
		background: $fsps;
		color: #fff;
	}
	&--phil {
		background: $phil;
		color: $color;
	}
	&--ped {
		background: $ped;
		color: $color;
	}
	&--econ {
		background: $econ;
		color: #fff;
	}
	&--pharm {
		background: $pharm;
		color: #fff;
	}
}
