.menu-content{
	margin: 0 0 $mMarginSmall;
	border: 1px solid $border;
	font-weight: bold;
	font-size: 0px;
	position: relative;
	white-space: nowrap;
	overflow: hidden;
	&__wrap{
		height: 48px;
		position: relative;
		white-space: nowrap;
		overflow: hidden;
	}
	&__list{
		@extend %reset-ul;
		overflow-x: auto;
		-webkit-overflow-scrolling: touch;
	}
	&__item{
		@extend %reset-ul-li;
		display: inline-block;
		vertical-align: top;
	}
	&__link{
		font-size: 14px;
		display: block;
		text-decoration: none;
		padding: 14px 15px;
		position: relative;
		transition: color .2s, background .2s;
		&.with-icon{
			padding-left: 35px;
			.icon{
				position: absolute;
				left: 15px;
				top: 50%;
				margin-top: -7px;
				color: $muni;
				transition: color .2s;
			}
		}
		&.is-active{
			background: $background;
		}
		@include hover() {
			&:hover {
				background: $muni;
				color: #fff;
				.icon {
					color: #fff;
				}
			}
		}
	}
	@media (min-width: $tablet) {
		margin: 0 0 $mMargin;
	}
}
